import { LocationStrategy } from '@angular/common';
import { ThrowStmt } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import { Router, RouterLink } from '@angular/router';
import moment from 'moment';
import { CategoryService } from 'src/app/Services/category.service';
import { DataService } from 'src/app/Services/data.service';
import { OrderService } from 'src/app/Services/order.service';
import { SuperAdminService } from 'src/app/Services/super-admin.service';
import { ValstoreRegisterService } from 'src/app/Services/valstore-register.service';


@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
  openslider: boolean = false;
  orderTotal = 0;
  storeType: any;
  storereview: any = [];
  prodreview: any = [];
  reviews: any = [];
  storeDetails: any = [];
  videoFiles2: any;
  userData: any;
  allSalestrip: any = [];
  orders: any;
  lengtg: any;
  ownerName: any;
  dashbord = "dashbord";

  constructor(private CategoryServiceObj: CategoryService,
    private superAdminService: SuperAdminService,
    private OrderServiceObj: OrderService,
    private data: DataService,
    private router: Router,
    private location: LocationStrategy,
    // private ValstoreRegisterServiceObj: ValstoreRegisterService
  ) {


    this.restrictBrowserBackButton();
    this.orderTotal = 0;

    this.CategoryServiceObj.getAllStoreReviews().subscribe((response: any) => {

      this.storereview = response.message;
      console.log(this.storereview);
      // this.isApproved=this.storereview.isApproved;
      this.storereview.forEach((element: any) => {
        //  element.createdAt=this.date.transform( element.createdAt, 'dd/mm/yyyy');
        element.createdAt = moment(element.createdAt).format('Do MMMM YYYY') // January

      });
      // this.storeName=this.data.getStoreName();
      this.data.setStoreReview(this.storereview);

    });

    this.CategoryServiceObj.getAllProductReviews().subscribe((response: any) => {

      this.prodreview = response.message;
      console.log(this.prodreview);

      // this.isApproved=this.prodreview.isApproved;
      this.prodreview.forEach((element: any) => {
        //  element.createdAt=this.date.transform( element.createdAt, 'dd/mm/yyyy');
        element.createdAt = moment(element.createdAt).format('Do MMMM YYYY') // January


      });
      this.data.setProductReview(this.prodreview);
      // this.storeName=this.data.getStoreName();
    });

    this.CategoryServiceObj.GetOrderTotal().subscribe(
      (response: any) => {
        ///  
        console.log(response);

        // this.orderTotal = response.data.orderId.length;
        this.orderTotal = response.data.length;

      },
      (error: any) => {
        // 
        //  alert("An error occured");
      }
    )


  }
  restrictBrowserBackButton() {
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  async ngOnInit() {


    this.storeType = localStorage.getItem('storeType');
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role[0];
    if (role === "superAdmin") {
      this.updateUserFormSuper();
    }

    //   this.storeDetails = await this.ValstoreRegisterServiceObj.getStoreDetail().toPromise();
    //  await this.storeDetails[0].storeImagesId.storeVideos.forEach((element: any) => {
    //     
    //     console.log(element);
    //     this.videoFiles2.push(element)
    //   });
    //    
    //   for (var i = 0; i < this.videoFiles2.length; i++) {
    //      
    //     this.videoFiles2[i] = "https://s3.wasabisys.com/valart/".concat(this.videoFiles2[i]);
    //   }
    
    var videos = await this.data.getVideo();

    this.videoFiles2 = videos[0];
    this.getSalestip();
    this.getAllOrders();
    this.getUserName();
  }
  viewDetails() {
    this.router.navigateByUrl("/admin/order-list");
  }
  getUserName() {
    const local: any = localStorage.getItem('currentUser')
    const res = JSON.parse(local);
    this.ownerName = res.user.ownerName;
    console.log(this.ownerName);

  }
  getAllOrders() {
    this.OrderServiceObj.getOrderByStoreId().subscribe((res: any) => {
      this.orders = res.results;
      this.lengtg = this.orders.length;
      console.log(this.lengtg);

    })
  }
  getSalestip() {
    this.superAdminService.getAllSalestrip().subscribe((res: any) => {
      this.allSalestrip = res.data;

      // console.log(this.allSalestrip);

    })
  }
  updateUserFormSuper() {
    var currentUser: any = localStorage.getItem('currentUser');
    this.userData = JSON.parse(currentUser);

    //  console.log(this.userData);
    const id = localStorage.getItem("storeFeth")

    this.userData.user.storeId = id;
    localStorage.setItem("currentUser", JSON.stringify(this.userData))
    //   console.log(this.userData);

  }
  // open mobile slider
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
