import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { VideoStreamService } from 'src/app/Services/video-stream.service';
import { NgxSpinnerService } from "ngx-spinner";
// import { VgTimeDisplay } from 'ngx-videogular/lib/controls/vg-time-display/vg-time-display';

@Component({
  selector: 'app-video-stream',
  templateUrl: './video-stream.component.html',
  styleUrls: ['./video-stream.component.scss']
})
export class VideoStreamComponent implements OnInit {
  openslider: boolean = false;
  @ViewChild("deleteDailog", { static: false }) deleteDailog: any;
  @ViewChild("alerts", { static: false }) alerts: any;
  @ViewChild("error", { static: false }) error: any;
  @ViewChild("streamDetailDailog", { static: false }) streamDetailDailog: any;
  videoLabels: any = [];
  storeId: any;
  videoCount: number = 0;
  deleteRecord: any;
  // ingestPoint:any ;
  // securityKey:any ;
  constructor(private videoStreamService: VideoStreamService, private router: Router, private dialog: MatDialog,
    private SpinnerService: NgxSpinnerService
  ) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    if (this.storeId) {
      this.storeId = this.storeId.user.storeId;
    }
  }

  ngOnInit(): void {
    this.SpinnerService.show();
    this.initialization();
  }

  initialization() {
    this.getvideoLables();
    this.getVideoData();
  }

  getvideoLables() {
    this.videoLabels = [
      { id: 1, name: 'Recorded Videos', value: 'recordedVideos', display: false, data: [] },
      { id: 2, name: 'Live Sessions', value: 'liveSessionVideos', display: false, data: [] },
      { id: 3, name: 'Packages', value: 'packageVideos', display: false, data: [] }
    ]
  }

  getVideoData() {
    //  // ;;
    this.videoStreamService.getVideoByStoreId('recordedVideo', this.storeId).subscribe((response: any) => {
      this.SpinnerService.hide();
      //  // ;;
      console.log(" recordedVideo :", response.data);
      this.videoLabels[0].data = response.data;
      this.videoCount = this.videoCount + response.data.length;
      this.videoStreamService.videoCount.next(this.videoCount);
    },
      (error: any) => {
        this.SpinnerService.hide();
      });
    this.videoStreamService.getVideoByStoreId('liveVideo', this.storeId).subscribe((response: any) => {
      this.SpinnerService.hide();
      //  // ;;
      console.log(" liveVideo :", response.data);
      this.videoLabels[1].data = response.data;
      this.videoCount = this.videoCount + response.data.length;
      this.videoStreamService.videoCount.next(this.videoCount);
    },
      (error: any) => {
        this.SpinnerService.hide();
      })
    this.videoStreamService.getVideoByStoreId('packageVideo', this.storeId).subscribe((response: any) => {
      this.SpinnerService.hide();
      //  // ;;
      console.log(" packageVideo :", response.data);
      this.videoLabels[2].data = response.data;
      this.videoCount = this.videoCount + response.length;
      this.videoStreamService.videoCount.next(this.videoCount);
    },
      (error: any) => {
        this.SpinnerService.hide();
      })
  }

  toggle(id: any) {
    this.videoLabels.forEach((item: any) => {
      if (item.id == id) {
        item.display = !item.display;
      }
    });
  }

  editVideo(data: any) {
    console.log(" edit Video", data);
    //  // ;;

    this.router.navigate(['/admin/add-video-stream'], { queryParams: { id: data._id, type: data.type } });
  }


  openDeleteModal(data: any) {
    this.deleteRecord = data;
    this.dialog.open(this.deleteDailog);
  }


  openStreamDetails(data: any) {
    console.log(" live video Data : ", data);

    // this.ingestPoint = data.ingestPoint;
    // this.securityKey = data.securityKey;
    // this.dialog.open(this.streamDetailDailog);
    this.router.navigate(['/admin/live-stream-detail', data._id]);
  }

  deleteVideo(data: any) {
    this.SpinnerService.show()
    this.videoStreamService.deleteVideo(data.type, data._id).subscribe(
      (response: any) => {
        this.SpinnerService.hide()
        this.dialog.closeAll();
        this.dialog.open(this.alerts);
        this.getVideoData();
      },
      (error: any) => {
        this.SpinnerService.hide()
        // alert(error);
        this.dialog.closeAll();
        this.dialog.open(this.error);
      });
  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }

  addVideo(){
    //  // ;;
    this.router.navigateByUrl('/admin/add-video-stream');
  }
}
