  constructor(private FoodMenuServiceobj: FoodMenuService, private location: LocationStrategy, private data: DataService, private spinnerService: NgxSpinnerService, private router: Router) {

    this.updationFood = this.data.getUpdateProduct();
    // this.extralbl=this.foodUpdateForm.value.extras;
    // this.taglbl=this.foodUpdateForm.value.tags;




      for(var i=0;i<this.updationFood.productImageId.pImage.length;i++){
    
        this.imgList.push("https://s3.wasabisys.com/homebyz/".concat(this.updationFood.productImageId.pImage[i]));
      }


    this.foodUpdateForm = new FormGroup({
      storeId: new FormControl(''),
      subCategoryId: new FormControl(''),
      name: new FormControl(''),
      price: new FormControl(''),
      noOfItem: new FormControl(''),
      weight: new FormControl(''),
      foodType: new FormControl(''),
      flavourQuotient: new FormControl(''),
      tags: new FormControl(''),
      extras: new FormControl(''),
      thumbnailImage: new FormControl(''),
      description: new FormControl(''),
      availability: new FormControl(''),
      length: new FormControl(),
      breadth: new FormControl(),
      height: new FormControl()


    });
    this.restrictBrowserBackButton()
  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  async ngOnInit() {
    this.MenuLabels = await this.FoodMenuServiceobj.getMenuLabels().toPromise();


    for (var i = 0; i < this.MenuLabels.length; i++) {

      if (this.MenuLabels[i]._id == this.updationFood.subCategoryId._id) {
        this.labelname = this.MenuLabels[i].subCatName;
      }
    }


    this.updationFood.foodImageId.thumbnailImage.forEach((element: any) => {
      this.imgList.push(element);
    });
    this.firstImg = this.imgList[0];
    ;
    // this.foodUpdateForm.controls.productimgs.setValue(this.imgList);

    this.imgList.splice(0, 1);

    this.imgList.splice(this.firstImg, 0);

    this.foodUpdateForm.controls.name.setValue(this.updationFood.name);
    this.foodUpdateForm.controls.description.setValue(this.updationFood.description);
    this.foodUpdateForm.controls.weight.setValue(this.updationFood.weight);
    this.foodUpdateForm.controls.price.setValue(this.updationFood.price);

    this.foodUpdateForm.controls.noOfItem.setValue(this.updationFood.noOfItem);
    this.foodUpdateForm.controls.foodType.setValue(this.updationFood.foodType);
    this.foodUpdateForm.controls.flavourQuotient.setValue(this.updationFood.flavourQuotient);
    this.foodUpdateForm.controls.tags.setValue(this.updationFood.tags);
    this.foodUpdateForm.controls.extras.setValue(this.updationFood.extras);
    this.foodUpdateForm.controls.availability.setValue(this.updationFood.availability);
    this.foodUpdateForm.controls.length.setValue(this.updationFood.length);
    this.foodUpdateForm.controls.breadth.setValue(this.updationFood.breadth);
    this.foodUpdateForm.controls.height.setValue(this.updationFood.height);


  }

