  updateFood() {

    this.MenuLabels.forEach((element: any) => {
      if (element.subCatName == this.foodUpdateForm.controls.subCategoryId.value) {
        this.foodUpdateForm.controls.subCategoryId.setValue(element._id)
      }

    });
    this.foodUpdateForm.controls.tags.setValue(this.tags);
    this.foodUpdateForm.controls.extras.setValue(this.extras);
    this.imgList.forEach((element: any) => {

      this.imagefiles.push(element);

    });
    console.log(this.imagefiles);
    this.oldImage = [];
    this.removedImages.forEach((element: any) => {
      var str = element.split("valart/", 2);
      this.oldImage.push(str[1]);


    });
    console.log(this.oldImage);


    //  this.foodUpdateForm.controls.productimgs.setValue(this.imagefiles);
    this.spinnerService.show();

    this.FoodMenuServiceobj.updateFood(this.foodUpdateForm.value, this.updationFood._id, this.imagefiles, this.oldImage).subscribe((response: any) => {
      console.log(response);
      this.spinnerService.hide();
      this.router.navigate(['/admin/menu'])
    })
  }

  showPic(event: any) {

    const file = (event.target).files[0];
    this.imagefiles.push(file);
    const reader = new FileReader();

    reader.onload = () => {

      this.imgurl = reader.result as string;
      this.imgUrllist.push(this.imgurl);
    }
    reader.readAsDataURL(file);


  }

  removeImg(img: any, index: any) {


    if (img == this.firstImg) {
      this.firstImg = this.imgList[0];
      this.imgList.splice(0, 1);
      this.foodUpdateForm.controls.thumbnailImage.setValue(this.imgList);
      this.removedImages.push(img)
    }
    else

      if (this.imgList.includes(img)) {
        this.imgList.splice(index, 1);
        this.foodUpdateForm.controls.thumbnailImage.setValue(this.imgList);
        this.removedImages.push(img)
      }


      else if (this.imgUrllist.includes(img)) {

        this.imgUrllist.splice(index, 1);
        // this.removedImages.push(img)
        this.foodUpdateForm.controls.thumbnailImage.setValue(this.imgUrllist);
      }




  }

  // open mobile slider
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
