import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { OrderService } from '../../../../Services/order.service';
import { SuperAdminService } from '../../../../Services/super-admin.service';
import { NgxSpinnerService } from 'ngx-spinner';

@Component({
  selector: 'app-payment-details',
  templateUrl: './payment-details.component.html',
  styleUrls: ['./payment-details.component.scss'],
})
export class PaymentDetailsComponent implements OnInit {
  openslider: boolean = false;
  productId: any;
  productDetailsObj: any;
  updateSettelment: boolean = false;
  confirmationPopup: boolean = false;
  constructor(
    private actRoute: ActivatedRoute,
    private OrderServiceObj: OrderService,
    private service: SuperAdminService,
    private spinnerService: NgxSpinnerService
  ) {}

  ngOnInit(): void {
    this.productId = this.actRoute.snapshot.paramMap.get('id');
    console.log('Id', this.productId);
    if (this.productId) {
      this.spinnerService.show();
      this.getOrderDetails(this.productId);
    }
  }
  clickEvent() {
    this.openslider = !this.openslider;
  }
  getOrderDetails(id: any) {
    this.OrderServiceObj.paymentDetailsById(id).subscribe((res: any) => {
      this.spinnerService.hide();
      this.productDetailsObj = res.results[0];
      this.doCalculation();
    });
  }
  downloadInvoice(id: any) {
    console.log('id', id);

    this.OrderServiceObj.downloadInvoice(id).subscribe((res: any) => {
      console.log('res', res);
      window.open(res.message.ownerInvoice, '_blank');
    });
  }
  onCheck() {
    this.confirmationPopup = false;
    // this.updateSettelment = false ;

    console.log('final op', this.updateSettelment);
    if (this.updateSettelment == true) {
      this.service
        .updateAdminPaymentStatus(this.productDetailsObj._id)
        .subscribe((res: any) => {
          console.log('res', res);
          this.getOrderDetails(this.productId);
        });
    }
  }
  closeModal() {
    this.confirmationPopup = false;
    this.updateSettelment = this.productDetailsObj.paymentReleased.status;
  }
  confirmSubmit(data: any) {
    this.updateSettelment = data.target.checked;
  }
  openPoup() {
    this.confirmationPopup = true;
  }

  doCalculation() {
    this.updateSettelment = this.productDetailsObj.paymentReleased.status;
    if (this.productDetailsObj.food.length != 0) {
      this.productDetailsObj.product = this.productDetailsObj.food;
    }

    let result = 0;
    let comission = 0;
    let individualGst = 0;
    let totalDeliveryCharge = 0;
    this.productDetailsObj.sellingPrice = 0;
    this.productDetailsObj.product.forEach((item: any) => {
      let outgoingAmt = 0;
      if (item.hasOwnProperty('productId')) {
        result = item.productSellingPrice - parseInt(item.productId.basePrice);

        if (this.productDetailsObj.storeId.gst == true) {
          individualGst =
            (parseInt(item.productId.basePrice) / 100) *
            this.productDetailsObj.taxRate;
          item.gst = individualGst.toFixed(2);
        } else {
          item.gst = 0;
        }

        item.comissionCharge = result;

        comission += result;
        if (item.shiprocket_delivery_charges) {
          if (this.productDetailsObj.product.length > 1) {
            totalDeliveryCharge += item.shiprocket_delivery_charges;
          } else {
            totalDeliveryCharge = item.shiprocket_delivery_charges;
          }
        } else {
          item.shiprocket_delivery_charges = 0;
        }
        outgoingAmt = parseInt(item.productId.basePrice) + parseInt(item.gst);
        item.recievedFrmHomebyz = outgoingAmt;
      } else {
        result = item.foodPrice - item.foodSellingPrice;
        individualGst = (item.foodPrice / 100) * this.productDetailsObj.taxRate;
        item.gst = individualGst.toFixed(2);
        if (item.shiprocket_delivery_charges) {
          if (this.productDetailsObj.product.length > 1) {
            totalDeliveryCharge += item.shiprocket_delivery_charges;
          } else {
            totalDeliveryCharge = item.shiprocket_delivery_charges;
          }
        } else {
          item.shiprocket_delivery_charges = 0;
        }
        item.comissionCharge = result;
        outgoingAmt = parseInt(item.productId.basePrice) + parseInt(item.gst);
        item.recievedFrmHomebyz = outgoingAmt;
      }

      this.productDetailsObj.totalDeliveryCharge =
        totalDeliveryCharge.toFixed(2);
    });
    let res: any;

    for (let i = 0; i < this.productDetailsObj.product.length; i++) {
      if (this.productDetailsObj.product.length > 1) {
        res =
          this.productDetailsObj.product[i].shiprocket_delivery_charges +
          this.productDetailsObj.product[
            this.productDetailsObj.product.length - 1
          ].shiprocket_delivery_charges +
          (this.productDetailsObj.product[0].recievedFrmHomebyz +
            this.productDetailsObj.product[
              this.productDetailsObj.product.length - 1
            ].recievedFrmHomebyz) +
          (this.productDetailsObj.product[0].comissionCharge +
            this.productDetailsObj.product[
              this.productDetailsObj.product.length - 1
            ].comissionCharge);
      } else {
        res =
          this.productDetailsObj.product[i].shiprocket_delivery_charges +
          this.productDetailsObj.product[i].recievedFrmHomebyz +
          this.productDetailsObj.product[i].comissionCharge;
      }
    }
    this.productDetailsObj.totalComissionCharges = comission.toFixed(2);
    this.productDetailsObj.grandTotalAmount =
      res + this.productDetailsObj.taxAmount;

    console.log('Final Obj', this.productDetailsObj);
  }
}
