import { LocationStrategy } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from "ngx-spinner";
import { DataService } from 'src/app/Services/data.service';
import { FoodMenuService } from 'src/app/Services/food-menu.service';

interface I_foodMenu {
  // id?:number;
  storeId?: string;
  subCategoryId?: string;
  name?: string;
  price?: string;
  noOfItem?: number;
  weight?: number;
  foodType?: string;
  flavourQuotient?: string;
  tags?: string;
  extras?: string;
  thumbnailImage?: any;
  availability?: boolean;
  pImage?: any;
}

@Component({
  selector: 'app-add-food-menu',
  templateUrl: './add-food-menu.component.html',
  styleUrls: ['./add-food-menu.component.scss'],
})
export class AddFoodMenuComponent implements OnInit {
  MenuLabels: any = [];
  payLoad_body: I_foodMenu;
  foodImgUrl: any = null;
  foodImgs: any = [];
  foodItemImgs: any = [];
  tags: any = [];
  extras: any = [];
  availability: boolean = false;
  priceList: any = [];
  editFlag: boolean = false;
  foodId: any;
  storeId: any;
  alertMsg: any;
  imageChangedEvent: any = null;
  openslider: boolean = false;
  foodMenuform = new FormGroup({
    storeId: new FormControl(''),
    subCategoryId: new FormControl(''),
    name: new FormControl(''),
    price: new FormControl(''),
    noOfItem: new FormControl(''),
    weight: new FormControl(''),
    foodType: new FormControl(''),
    flavourQuotient: new FormControl(''),
    tags: new FormControl(''),
    extras: new FormControl(''),
    thumbnailImage: new FormControl(''),
    description: new FormControl(''),
    availability: new FormControl(''),
    length: new FormControl(),
    breadth: new FormControl(),
    height: new FormControl()

  });

  constructor(
    private fb: FormBuilder,
    private foodMenuService: FoodMenuService,
    public router: Router,
    private actRoute: ActivatedRoute,
    private data: DataService,
    private SpinnerService: NgxSpinnerService,
    private location: LocationStrategy,
  ) {
    this.payLoad_body = {};
    this.priceList = [
      { id: 1, price: 10 },
      { id: 2, price: 20 },
      { id: 1, price: 30 }
    ];
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    this.storeId = this.storeId.user.storeId;

    this.restrictBrowserBackButton();
  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  ngOnInit(): void {
    this.foodId = this.actRoute.snapshot.paramMap.get("id");
    if (this.foodId) {
      this.editFlag = true;
      this.getFoodData(this.foodId);
      // this.foodMenuService.getFoodByStoreId(this.foodId).subscribe((response:any)=>{
      // 

      // });
    } else {
      this.editFlag = false;
      this.bindCreate(null);
    }
    this.getMenuLabels();
  }

  getFoodData(id: any) {
    this.foodMenuService.getFood(id).subscribe(
      (response: any) => {
        this.bindCreate(response[0])
      },
      (error: any) => {
        console.log("Error :", error);
        alert(error);
      }
    )
  }
  stripText(event: any) {
    const seperator = '^([0-9])';
    const maskSeperator = new RegExp(seperator, 'g');
    let result = maskSeperator.test(event.key);

    return result;
  }

  bindCreate(data: any) {

    if (data || data != null) {

      this.foodMenuform = this.fb.group({
        subCategoryId: [data.subCategoryId],
        name: [data.name],
        price: [data.price],
        noOfItem: [data.noOfItem],
        weight: [data.weight],
        foodType: [data.foodType],
        flavourQuotient: [data.flavourQuotient],
        tags: [data.tags],
        extras: [data.extras],
        thumbnailImage: [data.thumbnailImage],
        description: [data.description],
        availability: [data.availability],
        length: [data.length],
        breadth: [data.breadth],
        height: [data.height]


      });
      this.extras = data.extras;
      this.tags = data.tags;
    } else {
      this.foodMenuform = this.fb.group({
        subCategoryId: [''],
        name: [''],
        price: [''],
        noOfItem: [''],
        weight: [''],
        foodType: [''],
        flavourQuotient: [''],
        tags: [''],
        extras: [''],
        thumbnailImage: [''],
        description: [''],
        availability: [''],
        length: [''],
        breadth: [''],
        height: ['']
      });
    }
    console.log("final Data : ", this.foodMenuform.value);

  }

  getMenuLabels() {
    this.foodMenuService.getMenuLabels().subscribe((response: any) => {
      this.MenuLabels = response;
    });
  }

  showPic(event: any) {
    //  this.imageChangedEvent=event;
    let file = event.target.files[0];
    this.foodMenuform.controls.thumbnailImage.setValue(file, {
      emitModelToViewChange: false,
    });
    const reader = new FileReader();
    reader.onload = () => {
      this.foodImgUrl = reader.result as string;
      this.foodItemImgs.push(this.foodImgUrl);
    };
    this.foodImgs.push(file);
    reader.readAsDataURL(file);
  }

  saveFoodMenu() {
    this.foodMenuform.controls.thumbnailImage.setValue(this.foodImgs, {
      emitModelToViewChange: false,
    });
    this.foodMenuform.controls.extras.setValue(this.extras);
    this.foodMenuform.controls.tags.setValue(this.tags);
    // if (this.foodMenuform.valid) {
    // 

    // this.payLoad_body = this.foodMenuform.value;

    // this.payLoad_body.storeId = this.storeId;
    // this.payLoad_body.weight = parseInt(this.foodMenuform.controls.weight.value);
    // this.payLoad_body.noOfItem = parseInt(this.foodMenuform.controls.noOfItem.value);
    // this.payLoad_body.price =  this.payLoad_body.price?.toString();
    // this.payLoad_body.tags = this.tags;
    // this.payLoad_body.extras = this.extras;
    // this.payLoad_body.availability=this.foodMenuform.value.availability;
    // this.foodMenuform.value.thumbnailImage.forEach((element:any)=>{
    // this.payLoad_body.thumbnailImage.push(element);
    // });
    console.log(this.foodMenuform.value);

    console.log('this.payLoad_body : ', this.payLoad_body);
    if (this.editFlag == false) {

      this.SpinnerService.show();
      this.foodMenuService.createFood(this.foodMenuform).subscribe(

        (response: any) => {

          // ;

          this.router.navigate(['/admin/menu']);
          this.SpinnerService.hide();
          this.alertMsg = 'create Successfully';



        },
        (error: any) => {
          console.log('Error : ', error);
          alert(error);
        }
      );
    }
    // }else{
    //   this.foodMenuService.updateFood(this.foodId,this.payLoad_body).subscribe(
    //     (response: any) => {
    //       alert('updated Successfully');
    //       this.router.navigate(['/admin/menu']);
    //     },
    //     (error: any) => {
    //       console.log('Error : ', error);
    //       alert(error);
    //     }
    //   );
    // }
    // } else {
    //   alert('Enter Valid Data');
    // }
  }
  handleError() {
    this.alertMsg = null;
  }
  onKeyUp(event: KeyboardEvent): void {

    const inputtagsValue: string = this.foodMenuform.controls.tags.value;
    const inputextrasValue: string = this.foodMenuform.controls.extras.value;
    if (event.code === 'Backspace' && !inputtagsValue) {
      this.removeTag('tag');
      return;
    } else if (event.code === 'Backspace' && !inputextrasValue) {
      this.removeTag('extras');
      return;
    } else {
      if (
        event.code === 'Comma' ||
        (event.code === 'Space' && inputtagsValue)
      ) {
        this.addTag('tag', inputtagsValue);
        this.foodMenuform.controls.tags.setValue('');
      } else if (
        event.code === 'Comma' ||
        (event.code === 'Space' && inputextrasValue)
      ) {
        this.addTag('extras', inputextrasValue);
        this.foodMenuform.controls.extras.setValue('');
      }
    }
  }

  addTag(name: any, fieldName: string): void {
    if (name == 'tag') {
      this.tags.push(fieldName);
    } else if (name == 'extras') {
      this.extras.push(fieldName);
    }
  }

  removeTag(name: any, fieldName?: string): void {
    if (name == 'tag') {
      this.tags.forEach((element: any, index: any) => {
        if (element == fieldName) {
          this.tags.splice(index, 1);
          // this.addTagsToForm();
        }
      });
    } else if (name == 'extras') {
      this.extras.forEach((element: any, index: any) => {
        if (element == fieldName) {
          this.extras.splice(index, 1);
          // this.addExtrasToForm();
        }
      });
    }
  }

  addExtrasToForm(extra: string) {
    this.extras.push(extra);
    this.foodMenuform.controls.extras.setValue("");


  }

  addTagsToForm(tag: string) {
    this.tags.push(tag);
    this.foodMenuform.controls.tags.setValue("");

  }

  setFoodPic() {
    var file = this.data.getImage();

    this.foodImgUrl = this.data.getImageUrl();
    this.foodItemImgs.push(this.foodImgUrl);
    this.imageChangedEvent = null;

  }
  // open mobile slider
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
