import { LocationStrategy } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { NgxSpinnerService } from "ngx-spinner";
import { SuperAdminService } from 'src/app/Services/super-admin.service';
import { DataService } from '../../../Services/data.service';
import { ValstoreRegisterService } from '../../../Services/valstore-register.service';


@Component({
  selector: 'app-edit-bank-details',
  templateUrl: './edit-bank-details.component.html',
  styleUrls: ['./edit-bank-details.component.scss']
})
export class EditBankDetailsComponent implements OnInit {

  openslider: boolean = false;
  update: boolean = false;
  ifscErr: boolean = false;
  storeDetails: any;
  bankId: any;
  alertMsg: any;

  bankForm = new FormGroup({
    accountType: new FormControl(),
    accountNumber: new FormControl(),
    ifscCode: new FormControl(),
    branch: new FormControl(),
    accountHolderName: new FormControl()

  })
  constructor(private SpinnerService: NgxSpinnerService, private location: LocationStrategy, private ValstoreRegisterServiceObj: ValstoreRegisterService, private data: DataService, private superAdminService: SuperAdminService) {
    this.getStoreDetails();
    this.restrictBrowserBackButton();
  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }

  async ngOnInit() {
    //   this.storeDetails = await this.data.getStoreDetails();
    // //  // ;
    //  this.bankId=this.storeDetails[0].bankdetailId._id;

    //   this.bankForm.controls.accountType.setValue(this.storeDetails[0].bankdetailId.accountType);
    //   this.bankForm.controls.accountNumber.setValue(this.storeDetails[0].bankdetailId.accountNumber);
    //   this.bankForm.controls.ifscCode.setValue(this.storeDetails[0].bankdetailId.ifscCode);
    //   this.bankForm.controls.branch.setValue(this.storeDetails[0].bankdetailId.branch);
    //   this.bankForm.controls.accountHolderName.setValue(this.storeDetails[0].bankdetailId.accountHolderName);
  }
  handleError() {
    this.alertMsg = null;
  }
  getStoreDetails() {
    //   this.storeDetails = await this.data.getStoreDetails();
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const storeId = store.user.storeId;
    console.log(storeId);
    this.superAdminService.getStoreById(storeId).subscribe((res: any) => {
      console.log(res[0].bankdetailId);
      this.bankId = res[0].bankdetailId._id;

      this.bankForm.controls.accountType.setValue(res[0].bankdetailId.accountType);
      this.bankForm.controls.accountNumber.setValue(res[0].bankdetailId.accountNumber);
      this.bankForm.controls.ifscCode.setValue(res[0].bankdetailId.ifscCode);
      this.bankForm.controls.branch.setValue(res[0].bankdetailId.branch);
      this.bankForm.controls.accountHolderName.setValue(res[0].bankdetailId.accountHolderName);
    })

  }
  validateIFSC(ifsc: any) {
    var regexp = new RegExp("^[A-Z]{4}[0][A-Z0-9]{6}$");
    var test = regexp.test(ifsc);
    if (!test) {
      //  alert("PAN card number should be in correct format"); 
      this.ifscErr = true;
      this.update = true;


      return;
    }
    this.ifscErr = false;
    this.update = true;


  }

  updateBankDetails() {
    
    this.SpinnerService.show();

    this.ValstoreRegisterServiceObj.updateBankDetails(this.bankForm, this.bankId).subscribe(
      (response: any) => {
        this.SpinnerService.hide();

        console.log("store updated:" + response);
        this.alertMsg = "Bank Details Updated Successfully";
        window.location.reload();

      },
      (error: any) => {
        //  // ;
        this.SpinnerService.hide();


        this.alertMsg = "Bank Details Updatation Failed ";
        console.log(error);


        window.location.reload();
      }

    );


  }

  valuechange() {
    this.update = true;

  }


  // open mobile slider
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
