  createShirocketshipping(orderid: any) {
    console.log(orderid);

    this.SpinnerService.show();

    this.OrderServiceObj.createShiprocketOrder(orderid).subscribe(
      (response: any) => {

        console.log("gen shiprocket order response:")
        console.log(response);
        this.shipment_id = response.message.shiprocket_shipment_id;
        this.data.setShiprocketId(this.shipment_id);
        this.SpinnerService.hide();
        this.router.navigate(['/admin/order-detail/' + orderid]);
        this.alertMsg = "Shiprocket order Generated";
      },
      (error: any) => {
        this.SpinnerService.hide();
        this.alertMsg = "An error occured";

      }
    )
  }

  CancelOrder(orderid: any, productid: any) {
    // /cancelOrderBeforeDispatch
    //  // ;

    console.log(orderid);
    console.log(productid);
    this.SpinnerService.show();
    this.OrderServiceObj.cancelOrderProduct(orderid, productid).subscribe(
      (response: any) => {
        if (response.status == "success") {
          //  // ;
          console.log("cancel order response" + response);

          this.orderCancelled = true;
          this.SpinnerService.hide();
          this.alertMsg = response.message;
        }
        else {
          console.log("cancel order failed" + response.message);


          this.SpinnerService.hide();
          this.alertMsg = response.message;
        }
        this.Order = [];
        this.allOrders = [];
        this.fetchallOrders();
      },
      (error: any) => {
        console.log("cancel order error" + error);

        this.SpinnerService.hide();
        this.alertMsg = "An error occurred";
      }
    )
  }

  CancelOrderFood(orderid: any, foodid: any) {
    this.SpinnerService.show();
    this.OrderServiceObj.cancelOrderFood(orderid, foodid).subscribe(
      (response: any) => {
        if (response.status == "success") {
          
          console.log("cancel order response" + response);

          this.orderCancelled = true;
          this.SpinnerService.hide();
          this.alertMsg = response.message;
        }
        else {
          console.log("cancel order failed" + response.message);
          this.SpinnerService.hide();
          this.alertMsg = response.message;
        }
        this.Order = [];
        this.allOrders = [];
        this.fetchallOrders();
      },
      (error: any) => {
        console.log("cancel order error" + error);

        this.SpinnerService.hide();
        this.alertMsg = "An error occurred";
      }
    )
  }



