import { LocationStrategy } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { NgxSpinnerService } from "ngx-spinner";
import { CatalogueService } from 'src/app/Services/catalogue.service';
import { DataService } from 'src/app/Services/data.service';
import { ProductService } from 'src/app/Services/product.service';


interface HTMLInputEvent extends Event {
  target: HTMLInputElement & EventTarget;
}

@Component({
  selector: 'app-add-products',
  templateUrl: './add-products.component.html',
  styleUrls: ['./add-products.component.scss']
})
export class AddProductsComponent implements OnInit {
  productimgURL: any = null;
  tags: any = [];
  productimgs: any = [];
  catalogues: any = [];
  productForm = new FormGroup({});
  imageChangedEvent: any = null;
  productImgUrl: any = [];
  openslider: boolean = false;
  alertMsg: any;


  // @ViewChild ('catalog',{static:true})catalog:CatalogueHeaderComponent ;

  constructor(private CatalogueServiceobj: CatalogueService,
    private location: LocationStrategy, private router: Router, private SpinnerService: NgxSpinnerService, private ProductServiceObj: ProductService, private data: DataService) {


    this.CatalogueServiceobj.getAllCatalogue().subscribe((response) => {
      this.catalogues = response;
    });
    this.productForm = new FormGroup({
      fv: new FormControl(''),
      catalogueLbl: new FormControl(),
      prodTitle: new FormControl(),
      prodDesc: new FormControl(),
      prodWeight: new FormControl(),
      catlogTags: new FormControl(),
      price: new FormControl(),
      prodCount: new FormControl(),
      length: new FormControl(),
      breadth: new FormControl(),
      height: new FormControl(),
      productimgs: new FormControl(),
      availability: new FormControl()

    });
    this.restrictBrowserBackButton();
  }
  restrictBrowserBackButton() {
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  ngOnInit(): void {

  }
  stripText(event: any) {
    const seperator = '^([0-9])';
    const maskSeperator = new RegExp(seperator, 'g');
    let result = maskSeperator.test(event.key);

    return result;
  }
  showPic(event: any) {
    // this.imageChangedEvent=event;

    let file = (event.target).files[0];
    console.log("file:" + file);
    this.productForm.controls.productimgs.setValue(file, { emitModelToViewChange: false });
   
    const reader = new FileReader();
    reader.onload = () => {
      this.productimgURL = reader.result as string;
      this.productImgUrl.push(this.productimgURL);
    }
    this.productimgs.push(file);

    reader.readAsDataURL(file);

  }

  addProduct() {
    this.catalogues.forEach((element: any) => {
      if (element.subCatName == this.productForm.controls.catalogueLbl.value) {
        this.productForm.controls.catalogueLbl.setValue(element._id)
      }

    });
    this.productForm.controls.catlogTags.setValue(this.tags);

    this.productForm.controls.productimgs.setValue(this.productimgs, { emitModelToViewChange: false });
   
    console.log("Product Form:" + this.productForm.value);
    this.SpinnerService.show();
    this.ProductServiceObj.storeProduct(this.productForm).subscribe(
      (response: any) => {
        console.log("response:" + response);
        alert("product created successfully")
        this.SpinnerService.hide()
        this.router.navigate(['/admin/catalogues']);

        //  this.alertMsg = "product created successfully";

      },
      (error: any) => {
        this.SpinnerService.hide();
        this.alertMsg = "product creation failed";

      }
    )

  }
  handleError() {
    this.alertMsg = null;
  }


  onKeyUp(event: KeyboardEvent): void {
    const inputValue: string = this.productForm.controls.catlogTags.value;
    if (event.code === 'Backspace' && !inputValue) {
      this.removeTag();
      return;
    } else {
      if (event.code === 'Comma' || event.code === 'Space') {
        this.addTag(inputValue);
        this.productForm.controls.catlogTags.setValue('');
      }
    }
  }

  addTag(tag: string): void {
    this.tags.push(tag);
    console.log(this.tags);

  }

  removeTag(tag?: string): void {
    this.tags.forEach((element: any, index: any) => {
      if (element == tag) {
        this.tags.splice(index, 1);
      }
    });
  }

  addTagsToForm(tag: string) {
    
    this.tags.push(tag);
    console.log(this.tags);

    console.log(this.productForm.value.catlogTags);
  }


  setProductPic() {

    var file = this.data.getImage();

    this.productimgURL = this.data.getImageUrl();
    this.productimgs.push(file);
    this.productImgUrl.push(this.productimgURL);
    this.imageChangedEvent = null;

  }
  // open mobile slider
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
