import { LocationStrategy } from '@angular/common';
import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PubNubAngular } from 'pubnub-angular2';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-chat',
  templateUrl: './chat.component.html',
  //templateUrl: './chat.html',
  styleUrls: ['./chat.component.scss']
})
export class ChatComponent implements OnInit, OnDestroy {

  openslider: boolean | undefined;
  storeId: any;
  FeedbackData: any = [];
  RequestData: any = [];
  suggesionData: any = [];
  regForm!: FormGroup;
  messageType = "Request";
  constructor(private formsBuilder: FormBuilder, private adminService: AdminService, private location: LocationStrategy) {
    this.restrictBrowserBackButton();
  }
  // for restrict back button 
  restrictBrowserBackButton() {
    //private location: LocationStrategy,

    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  ngOnDestroy(): void {
    throw new Error('Method not implemented.');
  }
  ngOnInit(): void {
    this.regForm = this.formsBuilder.group({
      msgField: ['', Validators.required]
    });
    this.getUserDetails();
    this.getStoreQuery();
  }
  getUserDetails() {
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    this.storeId = store.user.storeId;

  }
  requestMsg() {
    this.messageType = "Request";
    console.log(this.messageType);

  }
  FeedbackMsg() {
    this.messageType = "Feedback";
    console.log(this.messageType);
  }
  SuggesionMsg() {
    this.messageType = "Suggesion";
    console.log(this.messageType);
  }
  sendMsg() {
    // if (this.regForm.invalid) {
    //   console.log("invalid");

    //   return
    // }
    console.log(this.regForm.get('msgField')?.value);

    const postData = {
      // message: this.regForm.get('msgField')?.value,
      message: "one more test message ",
      //  messagetype: this.messageType,
      messagetype: "Feedback",
      storeId: this.storeId,
    }
    this.regForm.reset();
    this.adminService.createStoreQuery(postData).subscribe((res: any) => {
      console.log(res);
      this.RequestData = [];
      this.FeedbackData = [];
      this.suggesionData = [];
      this.getStoreQuery();
    })
  }

  getStoreQuery() {
    this.adminService.getStoreQuery(this.storeId).subscribe((res: any) => {
      console.log(res);
      res.data.forEach((ele: any) => {
        ele.storeId.storeImagesId.storeLogo = "https://s3.wasabisys.com/valart/" + ele.storeId.storeImagesId.storeLogo;
      });
      this.FeedbackData = res.data.filter((x: any) => x.messagetype === "Feedback");
      this.RequestData = res.data.filter((x: any) => x.messagetype === "Request");
      this.suggesionData = res.data.filter((x: any) => x.messagetype === "Suggesion");


      // for (let i = 0; i < this.FeedbackData.length; i++) {
      //   this.FeedbackData.forEach((ele: any) => {
      //     for (var j = 0; j < ele.storeId.storeImagesId.storeLogo.length; j++) {
      //       ele.storeId.storeImagesId.storeLogo[j] = "https://s3.wasabisys.com/valart/".concat(ele.storeId.storeImagesId.storeLogo[j])
      //     }
      //   });
      // }
      console.log(this.FeedbackData);
      console.log(this.RequestData);
      console.log(this.suggesionData);

    })
  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
  //"16224408962470877" 
}
