  fetchAllProduct(){
   // this.storeId=this.data.getStoreId();
   var currentUser:any =  localStorage.getItem('currentUser');
   this. storeId=JSON.parse(currentUser);
   var token=this.storeId.token;
   const headers=new HttpHeaders({'Authorization': token})
   this.storeId=this.storeId.user.storeId;
   
    return this.http.get(`${environment.BASE_URL}/product/store/${this.storeId}`,{headers:headers});
  }


  updateProduct(product:any,id:any,imagefiles:any,oldImage:any){
  

    var formData: any = new FormData();
    formData.append("storeId",this.storeId);
    formData.append("subCategoryId",product.value.catalogueLbl);
    formData.append("name",product.value.prodTitle);
    formData.append("basePrice",product.value.price);
    formData.append("quantity",product.value.prodCount);
    formData.append("weight",product.value.prodWeight);
    for (var i = 0; i < imagefiles.length; i++) {
      formData.append("pImage", imagefiles[i]);
    }
    for(var i=0;i<oldImage.length;i++){
    formData.append("oldImage",oldImage[i]);
    }
   // formData.append("pImage",product.value.productimgs);
    formData.append("description",product.value.prodDesc);
    formData.append("tags",product.value.catlogTags);
    formData.append("length",product.value.length);
    formData.append("breadth",product.value.breadth);
    formData.append("height",product.value.height);
    formData.append("availability",product.value.availability);

    return this.http.put(`${environment.BASE_URL}/product/update/${id}`,formData);
  }

  deleteProduct(id:any)
  {
   
    return this.http.delete(`${environment.BASE_URL}/product/delete/${id}`);

  }
