import { DatePipe, LocationStrategy } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import printJS from 'print-js';
import { AdminService } from 'src/app/Services/admin.service';
import { DataService } from 'src/app/Services/data.service';
import { OrderService } from 'src/app/Services/order.service';

declare var require: any;


@Component({
  selector: 'app-order-details',
  templateUrl: './order-details.component.html',
  styleUrls: ['./order-details.component.scss']
})
export class OrderDetailsComponent implements OnInit {

  FileSaver = require('file-saver');
  shiprocketOrderData: any = [];
  orderCancelled: any;
  shiprocketOrderId: any = [];
  InvoiceUrl: any = null;
  isManifest = false;
  isLabels = false;
  isInvoice = false;
  pdf = false;
  pdfSrc: any;
  shipment_id: any = [];
  status: boolean = false;
  openslider: boolean = false;
  shippingInitiated: boolean = false;
  orderId: any;
  productId: any;
  constructor(
    private OrderServiceObj: OrderService,
    private data: DataService,
    private date: DatePipe,
    private SpinnerService: NgxSpinnerService,
    private adminService: AdminService,
    private router: Router,
    private location: LocationStrategy,
  ) {

    // this.fetchAllData();
    //console.log("Order:"+this.Order.orderId)
    this.restrictBrowserBackButton();
  }

  Order: any;

  async ngOnInit() {
    this.fetchAllData();

  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  openPdfViewer(url: any) {

    var fileURL = url;
    console.log(url);

    window.open(fileURL);

  }

  downloadPdf(pdfUrl: string, pdfName: string) {
    //const pdfUrl = './assets/sample.pdf';
    //const pdfName = 'your_pdf_file';
    console.log(pdfUrl);

    this.FileSaver.saveAs(pdfUrl, pdfName);
  }

  printPdf(pdflink: any) {
    console.log(pdflink);

    printJS({ printable: pdflink, type: 'pdf', showModal: true })

  }

  genManifest() {

    this.OrderServiceObj.generateManifest(this.shipment_id).subscribe((response: any) => {

      console.log("genManifest:");
      console.log(response);

      alert(response.message.message);

    });
  }

  genLabel() {

    this.OrderServiceObj.generateLabels(this.shipment_id).subscribe((response: any) => {

      console.log("genLabel:");
      console.log(response);

      console.log(response.message.label_url);
      alert(response.message.response)
    });

  }

  genInvoice() {
    console.log(this.shiprocketOrderId);
    this.OrderServiceObj.generateInvoice(this.shiprocketOrderId).subscribe((response: any) => {
      console.log("genInvoice:");
      console.log(response);
      window.open(response.message.invoice_url);
      this.InvoiceUrl = response.message.invoice_url;

    });
  }


  async fetchAllData() {
    
    var orders: any = [];
    orders = this.data.getOrder();
    this.Order = orders[0];
    console.log(this.Order);

    this.Order.orderDate = this.date.transform(this.Order.orderDate, 'dd-MM-yyyy');
    this.Order.product.forEach((element: any) => {
      element.productId.productImageId.pImage[0] = "https://s3.wasabisys.com/valart/".concat(element.productId.productImageId.pImage[0]);
    });
    this.Order.food.forEach((element: any) => {
      element.foodId.foodImageId.thumbnailImage[0] = "https://s3.wasabisys.com/valart/".concat(element.foodId.foodImageId.thumbnailImage[0]);

    });

    if (this.Order.shipRocketOrderGenerated == true) {

      this.shiprocketOrderData = await this.OrderServiceObj.fetchShiprocketOrder(this.Order._id).toPromise();
      console.log(this.shiprocketOrderData);

      this.shiprocketOrderData = await this.shiprocketOrderData.message;
      this.shiprocketOrderData.forEach((element: any) => {

        this.shipment_id.push(element.shiprocket_shipment_id);
        this.shiprocketOrderId.push(element.shiprocket_order_id);

      });
      console.log(this.shiprocketOrderId);

      //  this.shipment_id=this.shiprocketOrderData.shiprocket_shipment_id;

    }
    this.getOrderAndProdctId();
  }
  getOrderAndProdctId() {
    this.orderId = this.Order._id;

    if (this.Order.product.length != 0) {
      this.productId = this.Order.product[0].productId._id;

    }
    if (this.Order.food.length != 0) {
      this.productId = this.Order.food[0]._id;
    }
  }
  genarateAWB() {
    const postData = {
      "orderId": this.orderId,
      "productId": this.productId
    }
    console.log(postData);

    this.adminService.generteAWB(postData).subscribe((res: any) => {
      console.log(res);
      this.requestForShipment();
    });
  }
  requestForShipment() {
    console.log("at shipment");

    const postData = {
      "orderId": this.orderId,
      "productId": this.productId
    }
    this.adminService.requstFormShhipmentPickup(postData).subscribe((res: any) => {
      console.log(res);
    })
  }
  clickEvent() {
    this.status = !this.status;
  }
  clickEvent1(event: any) {
    this.openslider = !this.openslider;
  }

  InitiateShippingProduct(orderid: any, ProductId: any) {
    this.SpinnerService.show();
    this.OrderServiceObj.InitiateshippingProduct(orderid, ProductId).subscribe(
      (response: any) => {
        //  // ;
        if (response.status == "success") {
          //  // ;
          console.log("Initiate shipping" + response);
          alert(response.message);

          this.SpinnerService.hide();
        }
        else {
          //  // ;
          console.log("Initiate shipping failed" + response.message);
          alert(response.message);

          this.SpinnerService.hide();
        }
      },
      (error: any) => {
        
        console.log("Initiate Shipping error" + error);
        alert(error.error.message);
        this.SpinnerService.hide();
      })


  }

  InitiateShippingFood(orderid: any, foodid: any) {
    this.SpinnerService.show();
    this.OrderServiceObj.InitiateshippingFood(orderid, foodid).subscribe(
      (response: any) => {
        
        if (response.status == "success") {
          
          console.log("Initiate shipping" + response);
          alert(response.message);
          this.orderCancelled = true;
          this.SpinnerService.hide();
        }
        else {
          //  // ;
          console.log("Initiate shipping" + response.message);
          alert(response.message);

          this.SpinnerService.hide();
        }
      },
      (error: any) => {
        //  // ;
        console.log("Initiate shipping" + error);
        alert(error.error.message);
        this.SpinnerService.hide();
      })


  }

  CancelAfterDispatchOrderProduct(orderid: any, productid: any) {
    

    console.log(orderid);
    console.log(productid);
    this.SpinnerService.show();
    this.OrderServiceObj.cancelOrderAfterDispatchProduct(orderid, productid).subscribe(
      (response: any) => {
        if (response.status == "success") {
          //  // ;
          console.log("cancel order response" + response);
          alert(response.message);
          this.orderCancelled = true;
          this.SpinnerService.hide();
        }
        else {
          console.log("cancel order failed" + response.message);

          alert(response.message);
          this.SpinnerService.hide();

        }
      },
      (error: any) => {
        console.log("cancel order error" + error);
        alert("An error occurred");
        this.SpinnerService.hide();
      }
    )
  }

  CancelAfterDispatchOrderFood(orderid: any, foodid: any) {
    this.SpinnerService.show();
    this.OrderServiceObj.cancelOrderAfterDispatchFood(orderid, foodid).subscribe(
      (response: any) => {
        //     // ;
        if (response.status == "success") {
          //   // ;
          console.log("cancel order response" + response);
          alert(response.message);
          this.orderCancelled = true;
          this.SpinnerService.hide();
        }
        else {
          
          console.log("cancel order failed" + response.message);
          alert(response.message);

          this.SpinnerService.hide();
        }
      },
      (error: any) => {
        
        console.log("cancel order error" + error);
        alert("An error occurred");
        this.SpinnerService.hide();
      }
    )
  }


}
