import { LocationStrategy } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
// import { timeStamp } from 'console';
import { NgxSpinnerService } from "ngx-spinner";
import { DataService } from '../Services/data.service';
import { ValstoreRegisterService } from '../Services/valstore-register.service';

@Component({
  selector: 'app-edit-address',
  templateUrl: './edit-address.component.html',
  styleUrls: ['./edit-address.component.scss']
})
export class EditAddressComponent implements OnInit {
  update: boolean = false;
  storeDetails: any;
  storeEditForm: any;
  openslider: boolean = false;
  alertMsg: any;
  AddressEditForm = new FormGroup({
    postalAddress: new FormControl(),
    landMark: new FormControl(),
    city: new FormControl(),
    state: new FormControl(),
    zipcode: new FormControl()

  })
  constructor(private SpinnerService: NgxSpinnerService, private location: LocationStrategy, private ValstoreRegisterServiceObj: ValstoreRegisterService, private data: DataService) {
    this.restrictBrowserBackButton();
  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  async ngOnInit() {
    //  this.storeDetails = await this.data.getStoreDetails();
    this.storeDetails = await this.ValstoreRegisterServiceObj.getStoreDetail().toPromise();
    console.log(this.storeDetails);

    this.AddressEditForm.controls.postalAddress.setValue(this.storeDetails[0].postalAddress);
    this.AddressEditForm.controls.landMark.setValue(this.storeDetails[0].landmark);
    this.AddressEditForm.controls.city.setValue(this.storeDetails[0].city);
    this.AddressEditForm.controls.state.setValue(this.storeDetails[0].state);
    this.AddressEditForm.controls.zipcode.setValue(this.storeDetails[0].pincode);


  }

  valuechange() {
    this.update = true;

  }

  handleError() {
    this.alertMsg = null;
  }
  async updateInfo() {
    //  // ;
    this.SpinnerService.show();

    this.ValstoreRegisterServiceObj.updateAddress(this.AddressEditForm).subscribe(
      (response: any) => {
        this.SpinnerService.hide();

        console.log("store updated:" + response);
        this.alertMsg = "Address Updated Successfully";
        window.location.reload();

      },
      (error: any) => {
        this.SpinnerService.hide();



        this.alertMsg = "Address Updatation Failed ";

        window.location.reload();
      }

    );



  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
