import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ReviewListComponent } from '../review-list/review-list.component';

@Component({
  selector: 'app-review-header',
  templateUrl: './review-header.component.html',
  styleUrls: ['./review-header.component.scss']
})
export class ReviewHeaderComponent implements OnInit {
  valstoreClick: any;
  daybtn = "daybtn";
  daybtn2 = "daybtn2";

  constructor(private ReviewListComponentObj: ReviewListComponent, private router: Router) { }

  ngOnInit() {
    this.showStoreReview();
  }

  showStoreReview() {
    console.log("store");
    this.valstoreClick = false;
    this.ReviewListComponentObj.getStoreReviewData();
    // this.router.navigate(['/reviews']);

  }

  showProductReview() {
    console.log("product");

    this.valstoreClick = true;
    this.ReviewListComponentObj.getProductReviewData();
    // this.router.navigate(['/reviews']);

  }

}
