import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { AdminService } from 'src/app/Services/admin.service';
import { SuperAdminService } from 'src/app/Services/super-admin.service';
import { SetupValstoreComponent } from 'src/app/store-register/setup-valstore/setup-valstore.component';
import { times } from 'underscore';

@Component({
  selector: 'app-admin-siderbar',
  templateUrl: './admin-siderbar.component.html',
  styleUrls: ['./admin-siderbar.component.scss']
})
export class AdminSiderbarComponent implements OnInit {
  offer: boolean = false;
  catalogue: boolean = false;
  menuFlag: boolean = false;
  storeId: any;
  storeType: any;
  role: any;
  userData: any;
  userId: any;
  userActiated = false;
  storeStatus = false;
  constructor(private router: Router,
    private superService: SuperAdminService,
    private SpinnerService: NgxSpinnerService,
    private adminService: AdminService,
    // private setupValsoreComp: SetupValstoreComponent
  ) {
    // if (localStorage.getItem('currentUser')) {
    //   var currentUser: any = localStorage.getItem('currentUser');
    //   this.storeId = JSON.parse(currentUser);
    //   this.storeId = this.storeId.user.storeId;
    // }
    // if (localStorage.getItem('storeType')) {
    //   this.storeType = localStorage.getItem('storeType');
    // }
  }

  ngOnInit(): void {

    this.menuFlag = true;
    this.storeType = localStorage.getItem('storeType');
    const local: any = localStorage.getItem('currentUser')

    const res = JSON.parse(local);

    this.role = res.user.role[0];
    this.userId = res.user.id;
    if (localStorage.getItem('currentUser')) {
      var currentUser: any = localStorage.getItem('currentUser');
      this.storeId = JSON.parse(currentUser);
      this.storeId = this.storeId.user.storeId;
      // var usr = JSON.parse(currentUser);
      // var role = usr.user.role;
      // console.log(role);


      // if (role.includes("storeAdmin")) {
      //   this.storeStatus = true;
      // }
      // if (role.includes("superAdmin")) {
      //   this.storeStatus = true;
      // }
    }


    this.getStore();

  }
  updateDoc() {
    this.router.navigateByUrl('/valstoredoc');
  }
  setupStore() {
    this.router.navigateByUrl('/setupvalstore/' + 1);
    // this.setupValsoreComp.fromSidemenu = true;
  }
  getStore() {
    const storeS = localStorage.getItem("storeStatus");
    console.log(storeS);

    if (storeS != null) {

      if (storeS == "true") {
        this.storeStatus = true;
      }
      if (storeS == "false") {
        this.storeStatus = false;
      }

    }
    if (storeS == null) {
      this.adminService.getStoreById(this.storeId).subscribe((res: any) => {
        console.log(res);

        this.storeStatus = res[0].verified;
        if (this.role === "superAdmin") {
          this.storeStatus = true;
        }
      }, (error) => {

      })
    }

  }

  goToStore() {
    localStorage.setItem("storeStatus", "")
    localStorage.removeItem("storeDetails")
    var currentUser: any = localStorage.getItem('currentUser');
    this.userData = JSON.parse(currentUser);
    const superAdminStoreId = localStorage.getItem("superAdminStoreId");
    console.log("nevigating to suuperAdmin ");

    this.userData.user.storeId = superAdminStoreId;

    localStorage.removeItem("currentUser");
    console.log(this.userData);

    localStorage.setItem("currentUser", JSON.stringify(this.userData))
    this.router.navigateByUrl('/superAdmin/store-list');
  }
  ShippingAddress() {
    this.router.navigateByUrl('/add-address');
  }
  storeList() {
    this.router.navigateByUrl('/superAdmin/store-list');
  }
  userList() {
    this.router.navigateByUrl('/superAdmin/user-verify');
  }
  offerchange() {
    this.catalogue = false;
    this.offer = true;
    console.log("offer:" + this.offer);

  }

  Cataloguechange() {
    this.catalogue = true;
    this.offer = false;
  }

  Logout() {
    localStorage.removeItem('currentUser');
    localStorage.removeItem('storeid');
    localStorage.clear();
    this.router.navigate(['/']);
    alert("logout successfully...")
  }
}

