import { LocationStrategy } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { AdminService } from 'src/app/Services/admin.service';

@Component({
  selector: 'app-cutomer-list',
  templateUrl: './cutomer-list.component.html',
  styleUrls: ['./cutomer-list.component.scss']
})
export class CutomerListComponent implements OnInit {
  openslider: boolean = false;
  data: any;
  term: any;
  p: number = 1;
  customerLength: any;
  activeCustomer: any = [];
  customer: any = [];
  constructor(private adminService: AdminService, private location: LocationStrategy, private SpinnerService: NgxSpinnerService) {
    this.restrictBrowserBackButton();
  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  ngOnInit(): void {
    this.getCustores();
  }
  getCustores() {
    this.SpinnerService.show();
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);

    const storeId = store.user.storeId;
    console.log(storeId);

    this.adminService.getCustomers(storeId).subscribe((res: any) => {
      this.data = res.data;
      console.log(this.data);

      const c1 = [];
      for (let i of res.data[0].userId) {
        c1.push({
          activationSatus: i.activationSatus,
          email: i.email,
          fullName: i.fullName,
          pincode: i.pincode,
          code: i.code,
          profileImageId: i.profileImageId,
          state: i.state,
          _id: i._id,
          gender: i.gender,
          mobile: i.mobile,
          description: i.description,
          city: i.city,
          role: i.role,
          storeAdmin: false

        })
      }
      c1.forEach((ele: any) => {
        if (ele.profileImageId != null) {
          ele.profileImageId = "https://s3.wasabisys.com/valart/" + ele.profileImageId.image
        }
        if (ele.profileImageId == null) {
          ele.profileImageId = "assets/images/user.jpg"
        }
        if (ele.role.includes("storeAdmin")) {
          ele.storeAdmin = true
        }
      })

      this.customer = c1;
      console.log(this.customer);

      this.customerLength = res.data[0].userId.length;
      console.log(this.customerLength);

      this.SpinnerService.hide();

    },
      (error) => {
        this.SpinnerService.hide();
      })
  }
  SetActiveCustomer(ele: any) {
    this.activeCustomer = [];
    this.activeCustomer = ele;
    console.log(this.activeCustomer);

  }
  // open mobile slider
  clickEvent(event: any) {
    this.openslider = !this.openslider;
  }
}
