import { LocationStrategy } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { AdminService } from 'src/app/Services/admin.service';
import { DataService } from 'src/app/Services/data.service';
import { ValstoreRegisterService } from 'src/app/Services/valstore-register.service';

@Component({
  selector: 'app-message1',
  // templateUrl: './message1.component.html',
  templateUrl: './message.html',
  styleUrls: ['./message1.component.scss']
})
export class Message1Component implements OnInit {
  openslider: boolean | undefined;
  storeId: any;
  FeedbackData: any = [];
  RequestData: any = [];
  suggesionData: any = [];
  regForm!: FormGroup;
  messageType = "Request";
  users: any = [];
  requestUsers: any = [];
  FeedbackUsers: any = [];
  complentUser: any = [];
  destinationUserId: any;
  chatHistory: any = [];
  storeDetails: any;
  storeName: any;
  userimageURL = "";
  img: any;
  currentUserName = "";
  storeLogo = "";
  requestUserNotFound = false;
  completUserNotFound = false;
  feedbackUserNotFound = false;
  constructor(private formsBuilder: FormBuilder,private location: LocationStrategy, private SpinnerService: NgxSpinnerService, private ValstoreRegisterServiceObj: ValstoreRegisterService, private adminService: AdminService) {
    this.restrictBrowserBackButton();
   }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  ngOnInit(): void {
    this.regForm = this.formsBuilder.group({
      msgField: ['', Validators.required]
    });
    this.getUserDetails();
    this.requestMsg();
  }
  async getUserDetails() {
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    this.storeId = store.user.storeId;
    this.storeDetails = await this.ValstoreRegisterServiceObj.getStoreDetail().toPromise();
    this.img = this.storeDetails[0].storeImagesId.storeLogo;
    this.currentUserName = store.user.ownerName;
    this.userimageURL = "https://s3.wasabisys.com/valart/" + this.img;

    this.ValstoreRegisterServiceObj.getStoreDetail().subscribe((res: any) => {
      this.storeLogo = "https://s3.wasabisys.com/valart/" + res[0].storeImagesId.storeLogo;
    })
  }
  requestMsg() {
    this.chatHistory = [];
    this.messageType = "Request";
    console.log(this.messageType);
    this.requestUsers = [];
    this.feedbackUserNotFound = false;
    this.completUserNotFound = false;
    this.adminService.getStoreMessage(this.messageType, this.storeId).subscribe((res: any) => {
      console.log(res);
      if (res.message === "User not found.") {
        this.requestUserNotFound = true;
      } else {
        this.requestUserNotFound = false;
      }
      const demoData = res.data;
      for (let i of demoData) {
        this.requestUsers.push({
          fullName: i.fullName,
          userId: i._id,
          images: "https://s3.wasabisys.com/valart/" + res.data[0].profileImageId.image
        })
      }

      console.log(this.requestUsers);
    })
  }
  FeedbackMsg() {
    this.chatHistory = [];
    this.messageType = "Feedback";
    console.log(this.messageType);
    this.requestUserNotFound = false;
    this.completUserNotFound = false;
    this.FeedbackUsers = [];
    this.adminService.getStoreMessage(this.messageType, this.storeId).subscribe((res: any) => {
      console.log(res);
      if (res.message === "User not found.") {
        this.feedbackUserNotFound = true;
      } else {
        this.feedbackUserNotFound = false;
      }
      const demoData = res.data;
      for (let i of demoData) {
        this.FeedbackUsers.push({
          fullName: i.fullName,
          userId: i._id,
          images: "https://s3.wasabisys.com/valart/" + res.data[0].profileImageId.image
        })
      }



    })
  }

  ComplaintMsg() {
    this.chatHistory = [];
    this.messageType = "Complaint";
    console.log(this.messageType);
    this.complentUser = [];
    this.feedbackUserNotFound = false;
    this.requestUserNotFound = false;
    this.adminService.getStoreMessage(this.messageType, this.storeId).subscribe((res: any) => {
      const demoData = res.data;
      if (res.message === "User not found.") {
        this.completUserNotFound = true;
      } else {
        this.completUserNotFound = false;
      }
      for (let i of demoData) {
        this.complentUser.push({
          fullName: i.fullName,
          userId: i._id,
          images: "https://s3.wasabisys.com/valart/" + res.data[0].profileImageId.image
        })
      }
    })
  }
  setUserId(userId: any) {
    this.destinationUserId = userId;
    console.log(this.destinationUserId);
    this.chatHistory = [];
    this.getChatHistory();

  }
  getChatHistory() {
    this.SpinnerService.show();
    this.adminService.getChatHistory(this.destinationUserId, this.storeId, this.messageType).subscribe((res: any) => {
      console.log(res.data);
      const history = [];
      for (let i of res.data) {
        history.push({
          createdAt: i.createdAt,
          //   images: "https://s3.wasabisys.com/valart/" + i.images[0],
          image: i.userId.profileImageId.image,
          message: i.message,
          messagetype: i.messagetype,
          senderId: i.senderId,
          username: i.userId.fullName,

        })
      }
      history.forEach((ele: any) => {

        if (ele.image != null) {
          ele.image = "https://s3.wasabisys.com/valart/" + ele.image
        }
        if (ele.image == null) {
          ele.image = "assets/images/user.jpg"
        }
        if (ele.senderId === this.storeId) {
          ele.image = this.storeLogo,
            ele.username = this.currentUserName

        }
      });
      this.chatHistory = history
      console.log(this.chatHistory);


      this.SpinnerService.hide();
    }, (error) => {
      this.SpinnerService.hide();
    })



  }
  sendMsg() {
    if (this.regForm.invalid) {
      console.log("invalid");
      return
    }
    console.log(this.regForm.get('msgField')?.value);
    const postData = {
      message: this.regForm.get('msgField')?.value,
      messagetype: this.messageType,
      storeId: this.storeId,
      userId: this.destinationUserId,
      // images: this.img
      // images: this.userimageURL
    }
    console.log(postData);

    this.regForm.reset();
    this.adminService.createStoreQuery1(postData).subscribe((res: any) => {
      console.log(res);
      this.regForm.reset();
      this.getChatHistory();

    })
  }
  autoGrowTextZone(e: any) {
    e.target.style.height = "0px";
    e.target.style.height = (e.target.scrollHeight + 0) + "px";
  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
