import { Component, OnDestroy, OnInit, Input } from '@angular/core';
import { FoodMenuService } from 'src/app/Services/food-menu.service';

@Component({
  selector: 'app-food-header',
  templateUrl: './food-header.component.html',
  styleUrls: ['./food-header.component.scss']
})
export class FoodHeaderComponent implements OnInit {

  manageBtn:boolean=false;
  MenuLabels:any=[];
  menuCount:number = 0;
  menuItemCount:number = 0;
  forDashbord = false;
  @Input() fromDashordComp: any;
  constructor(private foodMenuService: FoodMenuService ) {

   }
   ngOnDestroy(): void {
    this.forDashbord = false;
    this.fromDashordComp = null;
  }
  ngOnInit(): void {
    this.initialization()
    if (this.fromDashordComp) {
      console.log(this.fromDashordComp);
      this.forDashbord = true
    }
  }

  initialization(){
    this.foodMenuService.getMenuLabels().subscribe((response:any)=>{
     let data = response;
     this.menuCount = data.length;
    });

    this.foodMenuService.getFoodByStoreId().subscribe(
      (res:any)=>{
        console.log("res: ",res);
        let data = res.message;
        data.forEach((element:any) => {
          this.menuItemCount += element.fooditems.length;
        });;
      },
      (err:any)=>{
        console.log("err: ",err);
      }
    )
  }

  changeManageBtn(){
    this.manageBtn=true;
  }

}
