import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { CatalogueService } from 'src/app/Services/catalogue.service';
import { CatalogueHeaderComponent } from '../catalogue-header/catalogue-header.component';
import { NgxSpinnerService } from "ngx-spinner";
import { MatDialog } from '@angular/material/dialog';
import { LocationStrategy } from '@angular/common';

class catalog {
  id: string = " ";
  dispdiv: boolean = false;
  subCatName: string = " ";
}

@Component({
  selector: 'app-manage-catalogue',
  templateUrl: './manage-catalogue.component.html',
  styleUrls: ['./manage-catalogue.component.scss']
})
export class ManageCatalogueComponent implements OnInit {
  @ViewChild("catalogue", { static: false }) catalogue: any;
  catalogToBeEdited: any
  label: any;
  newCatalogue = new catalog();
  catalogues: any = [];
  catToBeDeteletd: any;
  catToBeEditedId: any;
  openslider: boolean = false;
  alertMsg: any;
  constructor(private dialog: MatDialog, private location: LocationStrategy, private CatalogueServiceObj: CatalogueService, private router: Router, private SpinnerService: NgxSpinnerService) {

    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }

  ngOnInit(): void {

    this.getAllCatlogue();

    this.newCatalogue.id = " ";
    this.newCatalogue.dispdiv = false;
    this.newCatalogue.subCatName = " ";

  }
  getAllCatlogue() {
    this.catalogues = []
    this.CatalogueServiceObj.getAllCatalogue().subscribe((response: any) => {

      this.catalogues = [];
      this.catalogues = response;
      console.log(this.catalogues);
      this.SpinnerService.hide();
    });
  }

  // async addNewLabel() {
  //   if (this.label != "") {

  //     this.SpinnerService.show();
  //     let response = await this.CatalogueServiceObj.createCatalogue(this.label).toPromise();
  //     console.log(response);

  //     this.newCatalogue = new catalog();
  //     this.newCatalogue.dispdiv = false;
  //     this.newCatalogue.subCatName = this.label;
  //     this.catalogues.push(this.newCatalogue);
  //     console.log(this.catalogues);
  //     this.label = "";
  //     this.SpinnerService.hide();
  //   }

  // }
  async addNewLabel() {
    if (this.label != "") {

      this.SpinnerService.show();
      // let response = await this.CatalogueServiceObj.createCatalogue(this.label).toPromise();
      this.CatalogueServiceObj.createCatalogue(this.label).subscribe((res: any) => {
        console.log(res);
        this.getAllCatlogue();
      })
      // console.log(response);

      // this.newCatalogue = new catalog();
      // this.newCatalogue.dispdiv = false;
      // this.newCatalogue.subCatName = this.label;
      // this.catalogues.push(this.newCatalogue);
      // console.log(this.catalogues);
      // this.label = "";
      // this.SpinnerService.hide();
    }

  }

  handleError() {
    this.alertMsg = null;
  }
  async removecatlog(catName: any) {
    console.log(catName);


    this.catalogues.forEach((element: any) => {
      if (element.subCatName == catName) {
        // this.dialog.closeAll();
        // this.dialog.open(this.catalogue);
        this.catToBeDeteletd = element._id;
      }

    });
    //  var res: any;

    this.SpinnerService.show();
    this.CatalogueServiceObj.verifySubcategory(this.catToBeDeteletd).subscribe((rese: any) => {
      console.log(rese);
      this.SpinnerService.hide();
      //    This category contains proudct
      if (rese.message === "This category contains proudct ") {
        this.dialog.open(this.catalogue, {
          panelClass: ['modal_center', 'material_modal_center']
        });

      } else {
        this.getAllCatlogue();
      }
    }, (error) => {
      this.SpinnerService.hide();
    })



  }
  deleteCatlogLabelAndProd() {
    this.SpinnerService.show();
    this.CatalogueServiceObj.deleteCatalogLabels(this.catToBeDeteletd).subscribe((response: any) => {

      console.log(response);
      this.dialog.closeAll();
      this.SpinnerService.hide();

      window.location.reload();
      this.getAllCatlogue();

    });

    this.router.navigate(['/admin/mngcatlbls']);
  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }


  editCatalog(catlog: any) {
    this.catalogToBeEdited = catlog;
    this.catalogues.forEach((element: any) => {
      if (element.subCatName == catlog) {

        this.catToBeEditedId = element._id;
      }

    });

  }

  submitCatalog() {
    this.SpinnerService.show();
    this.CatalogueServiceObj.editCatalogue(this.catalogToBeEdited, this.catToBeEditedId).subscribe(
      (response: any) => {
        
        this.alertMsg = "Catalogue Name updated successfully"
        this.catalogues = [];
        this.getAllCatlogue();
      },
      (error: any) => {
        //  // ;
        alert(error);
      }

    )

  }
}
