import { DatePipe, LocationStrategy } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import moment from 'moment';
import { CategoryService } from '../../../Services/category.service';
import { DataService } from '../../../Services/data.service';



@Component({
  selector: 'app-review-list',
  templateUrl: './review-list.component.html',
  styleUrls: ['./review-list.component.scss']
})
export class ReviewListComponent implements OnInit {
  p: number = 1;
  showOption: boolean = false;
  reviewbox = "reviewbox";
  reviewbox2 = "reviewbox2";
  reviewImg2 = "reviewImg2";
  reviewImg = "reviewImg";
  reviews: any;
  prodreview: any = [];
  storereview: any = [];
  valstoreClick: any;
  daybtn = "daybtn";
  daybtn2 = "daybtn2";
  openslider: boolean = false;
  isApproved: boolean = false;
  storeDialog = false;
  regForm!: FormGroup;
  reviewId: any;
  storeReviewStatus: boolean = false;
  constructor(private date: DatePipe,
    private data: DataService,
    private CategoryServiceObj: CategoryService, private location: LocationStrategy,
    private router: Router, private formBUilder: FormBuilder) {
    this.restrictBrowserBackButton();
  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  async ngOnInit() {
    this.regForm = this.formBUilder.group({
      fReview: ['']
    })
    //  // ;
    // this.showStoreReview();
    // console.log("store");
    // this.valstoreClick = false;

    await this.getAllStorereview();
    this.reviews = this.storereview;
    this.getAllProductReview();
    this.showStoreReview();
    // this.cretatStoreReviewFeedbck();
  }
  async getAllStorereview() {
    var response: any = await this.CategoryServiceObj.getAllStoreReviews().toPromise();
  //  // ;
    this.storereview = response.message;
    console.log(this.storereview);
    const d1 = []

    for (let i of response.message) {
      d1.push({
        createdAt: i.createdAt,
        description: i.description,
        isApproved: i.isApproved,
        rating: i.rating,
        storeId: i.storeId,
        subject: i.subject,
        updatedAt: i.updatedAt,
        _id: i._id,
        userId: i.userId,
        showOption: false,
        image: i.userId.profileImageId.image
      });
    }
    console.log(d1);
    this.storereview = d1;

    // this.isApproved=this.storereview.isApproved;
    this.storereview.forEach((element: any) => {
      //  element.createdAt=this.date.transform( element.createdAt, 'dd/mm/yyyy');
      element.createdAt = moment(element.createdAt).format('Do MMMM YYYY') // January
      if (element.image != null) {
        element.image = "https://s3.wasabisys.com/valart/t_" + element.image;
      }
      if (element.image == null) {
        element.image = "assets/images/user.jpg";
      }
    });

    // this.storeName=this.data.getStoreName();

    console.log(this.storereview);

    this.data.setStoreReview(this.storereview);


  }
  setReviewId(reviewId: any) {
    this.storeReviewStatus = true;
    console.log("review for store :   " + this.storeReviewStatus);

    this.reviewId = "";
    this.reviewId = reviewId;
    console.log(reviewId);

  }
  setReviewIdforItem(reviewId: any) {
    this.storeReviewStatus = false;
    console.log("review for store :   " + this.storeReviewStatus);
    this.reviewId = "";
    this.reviewId = reviewId;
    console.log(reviewId);

  }
  sendFeedback() {
    console.log(this.regForm.value);
    if (this.regForm.invalid) {
      return console.log("invalid ");

    }
    const storeType = localStorage.getItem("storeType");
    console.log(storeType);
    if (this.storeReviewStatus === false) {
      if (storeType === "Food") {
        console.log("food post");

        this.cretatFoodtReviewFeedbck();
      }
      if (storeType === "Product") {
        console.log("Product post");
        this.cretatProductReviewFeedbck();
      }
      if (storeType === "Services") {
        console.log("service post");
        this.cretatServicestReviewFeedbck();
      }
    }

    if (this.storeReviewStatus === true) {
      console.log("store review ");

      this.cretatStoreReviewFeedbck();
    }
    //
  }
  getAllProductReview() {
    this.CategoryServiceObj.getAllProductReviews().subscribe(
      (response: any) => {

        this.prodreview = response.message;
        console.log(this.prodreview);
        // this.isApproved=this.prodreview.isApproved;
        const d2 = [];
        for (let i of response.message) {
          d2.push({
            createdAt: i.createdAt,
            description: i.description,
            isApproved: i.isApproved,
            rating: i.rating,
            storeId: i.storeId,
            subject: i.subject,
            updatedAt: i.updatedAt,
            _id: i._id,
            showOption: false,
            userId: i.userId,
            image: i.userId.profileImageId.image,
            storeAdmin: false
          });
        }
        // console.log(d2);
        this.prodreview = d2;
        this.prodreview.forEach((element: any) => {
          //  element.createdAt=this.date.transform( element.createdAt, 'dd/mm/yyyy');
          element.createdAt = moment(element.createdAt).format('Do MMMM YYYY') // January
          if (element.image != null) {
            element.image = "https://s3.wasabisys.com/valart/t_" + element.image;
          }
          if (element.image == null) {
            element.image = "assets/images/user.jpg";
          }
          if (element.userId.role.includes('storeAdmin')) {
            element.storeAdmin = true
          }
        })
        console.log(this.prodreview);

      },
    );
  }
  togglePopUp(ele: any) {
    console.log(ele);
    this.reviews.forEach((element: any) => {
      if (ele._id === element._id) {
        element.showOption = true
      } else {
        element.showOption = false
      }
    });
    console.log(this.reviews);

  }
  cretatStoreReviewFeedbck() {
    console.log("id" + this.reviewId);

    const feedbacks = this.regForm.get('fReview')?.value;

    this.CategoryServiceObj.cretatStoreReviewFeedbck(feedbacks, this.reviewId).subscribe((res: any) => {
      console.log(res);
      this.regForm.reset();
    }, (error) => {
      console.log(error);

    });

  }
  cretatProductReviewFeedbck() {

    const feedbacks = this.regForm.get('fReview')?.value;

    this.CategoryServiceObj.cretatProducteReviewFeedbck(feedbacks, this.reviewId).subscribe((res: any) => {
      console.log(res);
      this.regForm.reset();
    }, (error) => {
      console.log(error);

    });

  }
  cretatFoodtReviewFeedbck() {

    const feedbacks = this.regForm.get('fReview')?.value;;
    this.CategoryServiceObj.cretatFoodReviewFeedbck(feedbacks, this.reviewId).subscribe((res: any) => {
      console.log(res);
      this.regForm.reset();
    }, (error) => {
      console.log(error);

    });

  }
  cretatServicestReviewFeedbck() {

    const feedbacks = this.regForm.get('fReview')?.value;;

    this.CategoryServiceObj.cretatServicestReviewFeedbck(feedbacks, this.reviewId).subscribe((res: any) => {
      console.log(res);
      this.regForm.reset();
    }, (error) => {
      console.log(error);

    });

  }

  showStoreReview() {
    
    console.log("store");
    this.valstoreClick = false;
    this.getStoreReviewData();
  }
  showProductReview() {
    console.log("product");

    this.valstoreClick = true;
    this.getProductReviewData();
  }
  getStoreReviewData() {
    console.log(this.storereview);
    this.reviews = this.storereview;

  }

  getProductReviewData() {
    console.log(this.prodreview);
    this.reviews = this.prodreview;

  }
  clickEvent(event: any) {
    this.openslider = !this.openslider;
  }

  showDetailReview(review: any) {
    console.log(review);

    this.data.setReview(review);
    this.router.navigateByUrl('/admin/review-detail');
  }

}
