import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { DataService } from './data.service';
import { environment } from "../../environments/environment";
import { Observable } from 'rxjs';
@Injectable({
  providedIn: 'root'
})
export class FoodMenuService {
  verifySubCategoryToDelete(catToBeDeteletd: any): Observable<any> {
    var currentUser: any = localStorage.getItem('currentUser');
    const users = JSON.parse(currentUser);
    var token = users.token;
    const headers = new HttpHeaders({ 'Authorization': token });
    const url = environment.BASE_URL + "/subcategory/verify/food/" + catToBeDeteletd;
    return this.http.post(url, { headers: headers })
  }
  headers = new HttpHeaders({ 'Content-Type': 'application/json; charset=utf-8' });
  storeId: any;
  constructor(private http: HttpClient, private data: DataService) {
  }

  createFood(body: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    this.storeId = this.storeId.user.storeId;

    var formData: any = new FormData();
    formData.append("storeId", this.storeId);
    formData.append("weight", parseInt(body.value.weight));
    formData.append("noOfItem", parseInt(body.value.noOfItem));
    formData.append("price", body.value.price);
    formData.append("tags", body.value.tags);
    formData.append("extras", body.value.extras);
    formData.append("availability", body.value.availability);
    formData.append("subCategoryId", body.value.subCategoryId);
    formData.append("name", body.value.name);
    formData.append("foodType", body.value.foodType);
    formData.append("flavourQuotient", body.value.flavourQuotient);
    formData.append("description", body.value.description);
    formData.append("length", body.value.length);
    formData.append("breadth", body.value.breadth);
    formData.append("height", body.value.height)
    for (var i = 0; i < body.value.thumbnailImage.length; i++) {
      formData.append("thumbnailImage", body.value.thumbnailImage[i]);
    }

    // ;
    return this.http.post(`${environment.BASE_URL}/food/create`, formData);
  }

  getFood(id: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ 'Authorization': token })
    return this.http.get(`${environment.BASE_URL}/food/${id}`, { headers: headers });
  }

  updateFood(body: any, id: any, imagefiles: any, oldImage: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    this.storeId = this.storeId.user.storeId;

    var formData: any = new FormData();
    formData.append("storeId", this.storeId);
    formData.append("weight", body.weight);
    formData.append("noOfItem", parseInt(body.noOfItem));
    formData.append("price", body.price);
    formData.append("tags", body.tags);
    formData.append("extras", body.extras);
    formData.append("availability", body.availability);
    formData.append("subCategoryId", body.subCategoryId);
    formData.append("name", body.name);
    formData.append("foodType", body.foodType);
    formData.append("flavourQuotient", body.flavourQuotient);
    formData.append("description", body.description);
    formData.append("length", body.length);
    formData.append("breadth", body.breadth);
    formData.append("height", body.height);
    for (var i = 0; i < imagefiles.length; i++) {
      formData.append("thumbnailImage", imagefiles[i]);
    }
    for (var i = 0; i < oldImage.length; i++) {
      formData.append("oldImage", oldImage[i]);
    }
    return this.http.put(`${environment.BASE_URL}/food/update/${id}`, formData);
  }

  deleteFood(id: any) {
    return this.http.delete(`${environment.BASE_URL}/food/delete/${id}`);
  }

  getFoodByStoreId() {

    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ 'Authorization': token })
    this.storeId = this.storeId.user.storeId;
    return this.http.get(`${environment.BASE_URL}/food/store/sub-cat/${this.storeId}`, { headers: headers });
  }


  createMenuLabel(name: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    this.storeId = this.storeId.user.storeId;
    const body = {
      storeId: this.storeId,
      subCatName: name
    }
    return this.http.post(`${environment.BASE_URL}/subcategory/create`, body);
  }

  getMenuLabels() {

    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    this.storeId = this.storeId.user.storeId;

    return this.http.get(`${environment.BASE_URL}/subcategory/subcat-store/${this.storeId}`);
  }

  deleteMenuLabels(catid: any) {
    return this.http.delete(`${environment.BASE_URL}/subcategory/delete/${catid}`);
  }

}

