import { DatePipe, LocationStrategy } from '@angular/common';
import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { DataService } from '../../../Services/data.service';
import { ValstoreRegisterService } from '../../../Services/valstore-register.service';



@Component({
  selector: 'app-personal-info',
  templateUrl: './personal-info.component.html',
  styleUrls: ['./personal-info.component.scss']
})
export class PersonalInfoComponent implements OnInit {
  storeList: any;
  storeType: any;
  name: any = [];
  storeNames: any = [];
  storeId: any;
  currentUser: any;
  personalInfo: any;
  update: boolean = false;
  openslider: boolean = false;
  submitted = false;
  personalInfoForm = new FormGroup({
    firstName: new FormControl(),
    lastName: new FormControl(),
    Gender: new FormControl(),
    Dob: new FormControl(),
    email: new FormControl(),
    password: new FormControl(),
    mobno: new FormControl()
  });
  storeName: any;
  alertMsg: any;



  constructor(private ValstoreRegisterServiceObj: ValstoreRegisterService, private location: LocationStrategy, public datepipe: DatePipe, private data: DataService, private route: Router) {
    this.update = false;
    this.personalInfoForm = new FormGroup({
      firstName: new FormControl('', Validators.required),
      lastName: new FormControl('', Validators.required),
      Gender: new FormControl('', Validators.required),
      Dob: new FormControl('', Validators.required),
      email: new FormControl('', [Validators.required, Validators.email]),
      password: new FormControl('', Validators.required),
      mobno: new FormControl('', [Validators.pattern("^((\\+91-?)|0)?[0-9]{10}$"), Validators.required])
    });

    this.ValstoreRegisterServiceObj.getPersonalInfo().subscribe((res: any) => {

      this.personalInfo = res;
      this.name = this.personalInfo.fullName.split(' ');
      console.log(this.name);
      var len = this.name.length;
      this.personalInfoForm.controls.firstName.setValue(this.name[0]);
      this.personalInfoForm.controls.lastName.setValue(this.name[len - 1]);
      this.personalInfoForm.controls.Gender.setValue(this.personalInfo.gender);
      // this.personalInfoForm.controls.Dob.setValue(this.personalInfo.dob);
      this.personalInfoForm.controls.email.setValue(this.personalInfo.email);
      this.personalInfoForm.controls.password.setValue(this.personalInfo.password);
      this.personalInfoForm.controls.mobno.setValue(this.personalInfo.mobile);
      if (this.personalInfoForm.value.Gender == "male") {

        $('#male').click();
      }
      else {
        $('#female').click();
      }
      //   // ;

      let latest_date = this.datepipe.transform(this.personalInfo.dob, 'yyyy-MM-dd');
      this.personalInfoForm.controls.Dob.setValue(latest_date);

    });


    this.restrictBrowserBackButton();

  }
  get f() {
    return this.personalInfoForm.controls;
  }
  ngOnInit(): void {

  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  setStoreId(storeName: any) {
    var storeType;

    this.storeList.forEach((element: any) => {
      if (element.name == storeName) {
        this.storeId = element._id;
        storeType = element.storeType;
        //this.data.setStoreName(storeName);
        this.storeName = element.name;
      }
    });
    this.currentUser = localStorage.getItem('currentUser');
    this.currentUser = JSON.parse(this.currentUser);
    this.currentUser.user.storeId = this.storeId;
    this.currentUser.user.storeName = this.storeName;
    localStorage.setItem("currentUser", JSON.stringify(this.currentUser));
    // this.data.setStoreId(this.storeId);
    this.storeType = localStorage.getItem('storeType');
    //this.storeType = JSON.parse(this.storeType);
    this.storeType = storeType;
    localStorage.setItem('storeType', this.storeType);
    this.route.navigate(['/admin/dashboard']);


  }

  valuechange(val: any) {
    this.update = true;

  }


  updateInfo() {
    this.submitted = true;
    if (this.personalInfoForm.invalid) {
      return console.log("invalid");

    }
    this.personalInfo.fullName = this.personalInfoForm.value.firstName.concat(" " + this.personalInfoForm.value.lastName);
    this.personalInfo.gender = this.personalInfoForm.value.Gender;
    this.personalInfo.email = this.personalInfoForm.value.email;
    this.personalInfo.mobile = this.personalInfoForm.value.mobno;
    this.personalInfo.dob = this.personalInfoForm.value.Dob;

    this.ValstoreRegisterServiceObj.upatePersonalData(this.personalInfo).subscribe((response: any) => {
      console.log(response);
      this.submitted = false;
      this.alertMsg = response.message;
      this.route.navigateByUrl('/admin/dashbord');
      alert(response.message)
    }, (error) => {
      this.submitted = false;
    })


  }
  handleError() {
    this.alertMsg = null;
  }
  // open mobile slider
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
