import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';
import { ActivatedRoute, Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { PubNubAngular } from 'pubnub-angular2';
import { VideoStreamService } from 'src/app/Services/video-stream.service';

@Component({
  selector: 'app-live-stream-detail',
  templateUrl: './live-stream-detail.component.html',
  styleUrls: ['./live-stream-detail.component.scss']
})
export class LiveStreamDetailComponent implements OnInit {
  @ViewChild("streamDetailDailog",{static:false}) streamDetailDailog: any;
  openslider: boolean = false;
  liveSessionData:any;
  schedule:any;
  myChannel: any;
  chatBox: any = [];
  mainRom: any = [];
  ingestPoint:any;
  securityKey:any;
  sessionId:any;
  constructor(private actRoute:ActivatedRoute, private videoStreamService: VideoStreamService, private router:Router,
    private dialog: MatDialog, private SpinnerService: NgxSpinnerService, private _snackBar: MatSnackBar ) {
    this.schedule = []
   }

  ngOnInit(): void {
    this.sessionId = this.actRoute.snapshot.paramMap.get('id');
    this.getLiveSessionData()
  }

  loadMethod = (event:any) => {
    this.getLiveSessionData();
  }
  
  getLiveSessionData = () =>{
    this.videoStreamService.getDataById(this.sessionId, 'liveVideo').subscribe((res:any)=>{
      console.log("RES : ",res);
      this.liveSessionData = res.data;
      this.ingestPoint = res.data.ingestPoint;
      this.securityKey = res.data.securityKey;
      this.liveSessionData ? this.schedule = this.liveSessionData.scheduleId.schedule:'';
    },
    (err:any)=>{
      console.log("ERR : ",err);
    })
  }


  clickEvent(event:any) {
    this.openslider = !this.openslider;
  }

  openChat(ev:any){
    console.log("EV : ",ev.users);
    let usersData = JSON.stringify(ev.users);
    localStorage.setItem('usersData',usersData);

    localStorage.setItem('sessionId',this.sessionId);
    this.router.navigate(['/stream',{slotId:ev._id, playBackUrl:ev.playBackUrl}]);
  }

  openSnackMessage(value:any){
    this._snackBar.open(`${value} copied successfully ... `, 'X',{ 
      duration: 2000
    });
  }

}
