import { LocationStrategy } from '@angular/common';
import { Component, OnInit, ViewChild } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { Router } from '@angular/router';
import { DataService } from 'src/app/Services/data.service';
import { FoodMenuService } from 'src/app/Services/food-menu.service';

@Component({
  selector: 'app-food-menu',
  templateUrl: './food-menu.component.html',
  styleUrls: ['./food-menu.component.scss']
})
export class FoodMenuComponent implements OnInit {

  @ViewChild("deleteDailog", { static: false }) deleteDailog: any;
  MenuLabels: any = [];
  openslider: boolean = false;
  alertMsg: any;
  constructor(private foodMenuService: FoodMenuService, private location: LocationStrategy, private router: Router, private dialog: MatDialog, private data: DataService) {
    this.restrictBrowserBackButton();
  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  ngOnInit(): void {
    this.initialization();
  }

  initialization() {
    // this.getMenuLables();
    this.getFoodData();
  }

  // getMenuLables(){
  //   this.foodMenuService.getMenuLabels().subscribe((response:any)=>{
  //     console.log(" MenuLabels :", response);
  //     this.MenuLabels=response;
  //     this.MenuLabels.forEach((element:any) => {
  //       element.display = false;
  //     });
  //   });
  // }

  getFoodData() {

    this.foodMenuService.getFoodByStoreId().subscribe((response: any) => {

      console.log(" getAllFoodData :", response.message);
      this.MenuLabels = response.message;
      this.MenuLabels.forEach((element: any, index: number) => {
        element.id = index + 1;
        element.display = false;
      });
      console.log(" after Data :", this.MenuLabels);
      for (var k = 0; k < this.MenuLabels.length; k++) {

        for (var j = 0; j < this.MenuLabels[k].fooditems.length; j++) {
          console.log("<this.MenuLabels[k].fooditems" + this.MenuLabels[k].fooditems);
          //   for(var i=0;i<this.MenuLabels[k].fooditems[j].foodImageId.thumbnailImage.length;i++)
          // 

          //   console.log("before conversion"+this.MenuLabels[k].fooditems[j].foodImageId.thumbnailImage);
          //  var link="https://s3.wasabisys.com/valart/".concat(this.MenuLabels[k].fooditems[j].foodImageId.thumbnailImage);
          //  this.MenuLabels[k].fooditems[j].foodImageId.thumbnailImage[i]=link;
          //   console.log("after conversion"+this.MenuLabels[k].fooditems[j].foodImageId.thumbnailImage)
          // }
          var imgarr = this.MenuLabels[k].fooditems[j].foodImageId.thumbnailImage;

          //   imgarr.forEach((element:any) => {
          //   
          // var link="https://s3.wasabisys.com/valart/".concat(element);
          // element=link;

          //   });
          for (var i = 0; i < imgarr.length; i++) {
            imgarr[i] = "https://s3.wasabisys.com/homebyz/t_".concat(imgarr[i]);
          }

          this.MenuLabels[k].fooditems[j].foodImageId.thumbnailImage = imgarr;

        }
      }
    });
  }

  toggle(id: any) {
    this.MenuLabels.forEach((item: any) => {
      if (item.id == id) {
        item.display = !item.display;
      }
    });
  }

  editFood(food: any) {
    console.log(" edit Food");
    this.data.setUpdateProduct(food);
    this.router.navigate(['/admin/updatefood']);
  }


  deleteFood(id: any) {
    // this.dialog.open(this.deleteDailog, { data: "some data" });
    // console.log("ID : ",id)
    // let action = confirm("Press a button!");
    // if (action == true) {
    this.foodMenuService.deleteFood(id).subscribe(
      (response: any) => {
        this.alertMsg = 'Deleted Successfully';
        this.getFoodData();
        window.location.reload();
      },
      (error: any) => {
        this.alertMsg = error;
      });
    //}
  }
  handleError() {
    this.alertMsg = null;
  }
  // open mobile slider
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
