import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { DataService } from './data.service';
import { environment } from "../../environments/environment";
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class CatalogueService {
  verifySubcategory(catToBeDeteletd: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ 'Authorization': token })
    var storeType = localStorage.getItem('storeType');
    console.log(storeType);
    var type1 = ""

    if (storeType === "Product") {
      type1 = "product";

    }
    if (storeType === "Food") {
      type1 = "food";
    }
    const postData = {
      type: type1
    }
    return this.http.post(`${environment.BASE_URL}/subcategory/verify/${type1}/${catToBeDeteletd}`, postData, { headers: headers });
    // return this.http.post(`${environment.BASE_URL}/verify/${type1}/${catToBeDeteletd}`, postData, { headers: headers });
  }

  constructor(private http: HttpClient, private data: DataService) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    this.storeId = this.storeId.user.storeId;
    // this.storeId=this.data.getStoreId();
  }
  storeId: any;

  // createCatalogue(catName: any) {
  //  

  //   const body = {
  //     storeId: this.storeId,
  //     subCatName: catName
  //   }

  //   return this.http.post(`${environment.BASE_URL}/subcategory/create`, body)
  // }
  createCatalogue(catName: any): Observable<any> {
   

    const body = {
      storeId: this.storeId,
      subCatName: catName
    }

    return this.http.post(`${environment.BASE_URL}/subcategory/create`, body)
  }

  getAllCatalogue() {
   
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ 'Authorization': token })
    this.storeId = this.storeId.user.storeId;

    return this.http.get(`${environment.BASE_URL}/subcategory/subcat-store/${this.storeId}`, { headers: headers });

  }

  deleteCatalogLabels(catid: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    var token = this.storeId.token;
    const headers = new HttpHeaders({ 'Authorization': token })
    var storeType = localStorage.getItem('storeType');
    console.log(storeType);
    var type1 = ""
    if (storeType === "Product") {
      type1 = "product";
    }
    if (storeType === "Food") {
      type1 = "food";
    }

    return this.http.delete(`${environment.BASE_URL}/subcategory/delete/${type1}/${catid}`, { headers: headers });

  }

  editCatalogue(catName: any, catId: any) {
    var currentUser: any = localStorage.getItem('currentUser');
    this.storeId = JSON.parse(currentUser);
    const body = {
      subCatName: catName
    }

    return this.http.put(`${environment.BASE_URL}/subcategory/update/${catId}`, body);

  }
}
