import { Component, OnDestroy, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { PubNubAngular } from 'pubnub-angular2';
import { AdminService } from 'src/app/Services/admin.service';
@Component({
  selector: 'app-message',
  templateUrl: './message.component.html',
  styleUrls: ['./message.component.scss']
})
export class MessageComponent implements OnInit, OnDestroy {

  openslider: boolean = false;
  pubnub!: PubNubAngular;
  userChannel: any;
  data: any = [];
  msg: any;
  msg1: any;
  regForm!: FormGroup;
  myChannel: any;
  allMessage: any = [];
  chatBox: any = [];
  mainRom: any = [];
  storeId: any;
  FeedbackData: any = [];
  RequestData: any = [];
  suggesionData: any = [];
  // our 609ba3d717766000153ff613

  constructor(pubnub1: PubNubAngular, private formsBuilder: FormBuilder, private adminService: AdminService) {
    this.pubnub = pubnub1;
    pubnub1.init({
      publishKey: 'pub-c-05093612-76ee-4cbd-84f5-b132cc43a93b',
      subscribeKey: 'sub-c-fcf68878-beaf-11eb-8415-662615fc053c'
    });

    this.getUserDetails();
    if (this.pubnub) {
      this.pubnub.subscribe({
        channels: [this.myChannel],
      })
      this.pubnub.addListener({

        message: (m: any) => {
          const tempMain = {
            id: m.message.id,
            author: m.publisher,
            content: m.message.content,
            timetoken: m.timetoken,
            time: m.message.time,
            message: m.message,
            userName: "demo user",
            imageUrl: "assets/images/Patron/image-83.png",
            msgStatus: "active"
          }
          // this.mainRom.push(tempMain);
          console.log(m);
          const newMsg = {
            id: m.message.id,
            author: m.publisher,
            content: m.message.content,
            timetoken: m.timetoken,
            time: m.message.time,
            message: m.message,
          }
          console.log(newMsg);
          this.chatBox.push(newMsg);

        }
      })
    }
  }
  ngOnDestroy(): void {
    this.pubnub.unsubscribeAll();
    this.storeId = "";
  }

  ngOnInit(): void {
    this.regForm = this.formsBuilder.group({
      msgField: ['', Validators.required]
    })

    this.fetChMessage();
    this.getMainRoomHistory();
    this.getStoreQuery();
  }
  getMainRoomHistory() {


    this.pubnub.subscribe({
      channels: [this.myChannel],
    });

    this.pubnub.fetchMessages(
      {
        // channels: [this.myChannel],
        channels: [this.myChannel],
        count: 100,
        end: 16221986217189860
      },
      (status, response) => {
        const data = response.channels['vijayz%40anomaly.co.in']
        for (let i of data) {
          this.mainRom.push(
            {
              id: i.message.id,
              author: i.uuid,
              content: i.message.content,
              timetoken: i.timetoken,
              time: i.message.time,
              message: i.message,
              role: ""
            }
          )
        }
        console.log(this.mainRom);

      }
    )
  }

  getUserDetails() {
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    this.storeId = store.user.storeId;
    this.myChannel = store.user.email;

  }

  sendMsg() {
    if (this.regForm.invalid) {
      return
    }
    this.pubnub.setUUID(this.myChannel);
    let d1 = new Date();
    let d2 = d1.toLocaleTimeString();
    const msg = {
      // content: "hello from vijay",
      time: d2,
      content: this.regForm.get('msgField')?.value,
      id: Math.random().toString(16).substr(2)
    }
    console.log(this.msg1);

    console.log(msg);
    this.pubnub.publish({
      channel: "sandhyap@anomaly.co.in",
      message: msg,
      messageType: "Request"
    }, (status: any, response: any) => {
      console.log(status);
      console.log(response);
      this.regForm.reset();
      this.fetChMessage();
    });
  }

  fetChMessage() {
    let date = new Date().getTime();
    console.log('end----', date * 10000);
    let d = date * 10000;
    let data = d.toString();
    console.log('data-------------45------', data);
    this.pubnub.fetchMessages(
      {
        // channels: [this.myChannel],
        channels: [this.myChannel, "sandhyap@anomaly.co.in"],
        count: 100,
        end: 16221986217189860
      },
      (status, response) => {
        console.log('HISTOREY');
        console.log(response);

        let user1 = response.channels;
        let user1Data: any[] = [];
        console.log('user1', user1);
        for (var key in user1) {
          if (user1.hasOwnProperty(key)) {

            for (let val = 0; val < user1[key].length; val++) {
              //   console.log('baaba', user1[key][val].timetoken);
              user1Data = [...user1Data,
              {
                id: user1[key][val].message.id,
                author: user1[key][val].uuid,
                content: user1[key][val].message.content,
                timetoken: user1[key][val].timetoken,
                time: user1[key][val].message.time,
                message: user1[key][val].message,
                role: ""
              }];
            }
            //  console.log('real12', user1Data);
            let tempdata = user1Data;
            for (let i = 0; i < tempdata.length - 1; i++) {
              for (let j = 0; j < tempdata.length - 1; j++) {
                if (tempdata[j].timetoken > tempdata[j + 1].timetoken) {
                  let temp = tempdata[j];
                  tempdata[j] = tempdata[j + 1];
                  tempdata[j + 1] = temp;
                }
              }

            }
            tempdata.forEach(ele => {
              if (ele.author === this.myChannel) {
                ele.role = "user"
              } else {
                ele.role = "guest"
              }
            })
            this.chatBox = tempdata;
            this.chatBox = this.chatBox.filter((x: { author: string; }) => x.author === this.myChannel || x.author === "sandhyap@anomaly.co.in")
            console.log('sort', this.chatBox);
          }
        }
      }
    )
  }

  postStoreQuery() {
    const postData = {
      message: "Hi this is my second request",
      //  messagetype: "Request",
       messagetype: "Feedback",
     // messagetype: "Suggesion",
      storeId: this.storeId,
      //  images: 
    }
    this.adminService.createStoreQuery(postData).subscribe((res: any) => {
      console.log(res);

    })
  }

  getStoreQuery() {
    this.adminService.getStoreQuery(this.storeId).subscribe((res: any) => {
       console.log(res);
      this.FeedbackData = res.data.filter((x: any) => x.messagetype === "Feedback");
      this.RequestData = res.data.filter((x: any) => x.messagetype === "Request");
      this.suggesionData = res.data.filter((x: any) => x.messagetype === "Suggesion");
      console.log(this.FeedbackData);
      console.log(this.RequestData);
      console.log(this.suggesionData);

    })
  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
  //"16224408962470877" 
}
