import { Component, OnInit } from '@angular/core';

import moment from 'moment';
import { Router } from '@angular/router';
import { CategoryService } from 'src/app/Services/category.service';
import { DataService } from 'src/app/Services/data.service';
import { LocationStrategy } from '@angular/common';


@Component({
  selector: 'app-reviews',
  templateUrl: './reviews.component.html',
  styleUrls: ['./reviews.component.scss']
})
export class ReviewsComponent implements OnInit {
  openslider: boolean = false;
  reviews: any;
  storeName: any;
  storeId: any;
  approvalres: any;
  isApproved: boolean = false;
  reportReason: any;
  reportMessage: any;

  constructor(private CategoryServiceObj: CategoryService, private location: LocationStrategy, private data: DataService, private route: Router) {
    this.restrictBrowserBackButton();
  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  ngOnInit() {
    // ;
    this.reviews = this.data.getReview();
    // this.reviews.createdAt= moment(this.reviews.createdAt).format('Do MMMM YYYY') // January
    console.log(this.reviews);

    // this.storeName=this.data.getStoreName();


  }

  clickEvent(event: any) {
    this.openslider = !this.openslider;
  }


  Reviewal(status: any, id: any, type: string) {
    var approve: any = status;


    if (type == "store") {
      this.CategoryServiceObj.changeApproval(id, approve).subscribe((response: any) => {

        this.approvalres = response;

      });
    }
    else {
      this.CategoryServiceObj.changeApprovalforProduct(id, approve).subscribe((response: any) => {

        this.approvalres = response;

      });

    }

  }

  reportAndDelete(reviewid: any, type: any, id: any) {
    // ;
    console.log("reportReason", +this.reportReason);
    console.log("reportMessage", +this.reportMessage);
    if (type == "product") {
      this.CategoryServiceObj.DeleteProductReview(reviewid, this.reportReason, this.reportMessage, id).then(
        (response: any) => {
          // ;
          alert(response.data.message);
          this.route.navigate(['/admin/reviews'])

        },
        (error: any) => {
          // ;
          alert(error);

        }

      )
    }
    if (type == "food") {
      this.CategoryServiceObj.DeleteFoodReview(reviewid, this.reportReason, this.reportMessage, id).then(
        (response: any) => {
          // ;
          alert(response.data.message);
          this.route.navigate(['/admin/reviews'])
        },
        (error: any) => {
          // ;
          alert(error);

        }

      )
    }
    if (type == "store") {
      this.CategoryServiceObj.DeleteStoreReview(id, this.reportReason, this.reportMessage).then(
        (Response: any) => {
          // ;
          alert(Response.data.message);
          this.route.navigate(['/admin/reviews'])
        },
        (error: any) => {
          // ;
          alert(error);
        }

      )
    }


  }


}
