import { ThrowStmt } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl } from '@angular/forms';
import { Router } from '@angular/router';
import { CatalogueService } from 'src/app/Services/catalogue.service';
import { DataService } from 'src/app/Services/data.service';
import { ProductService } from 'src/app/Services/product.service';
import { NgxSpinnerService } from "ngx-spinner";
import { LocationStrategy } from '@angular/common';
@Component({
  selector: 'app-update-product',
  templateUrl: './update-product.component.html',
  styleUrls: ['./update-product.component.scss']
})
export class UpdateProductComponent implements OnInit {
  tags: any = [];
  catalogues: any = [];
  updationProduct: any;
  labelname: any
  imgList: any = [];
  imagefiles: any = [];
  imgUrllist: any = [];
  imgurl: any;
  firstImg: any;
  oldImage: any = [];
  removedImages: any = [];
  openslider: boolean = false;

  prodUpdateForm = new FormGroup({});
  constructor(private CatalogueServiceobj: CatalogueService, private location: LocationStrategy, private data: DataService, private spinnerService: NgxSpinnerService, private ProductServiceObj: ProductService, private router: Router) {

    this.updationProduct = this.data.getUpdateProduct();

    this.CatalogueServiceobj.getAllCatalogue().subscribe((response: any) => {

      this.catalogues = response;
      for (var i = 0; i < this.catalogues.length; i++) {

        if (this.catalogues[i]._id == this.updationProduct.subCategoryId) {
          this.labelname = this.catalogues[i].subCatName;
        }
      }
    });

      for(var i=0;i<this.updationProduct.productImageId.pImage.length;i++){
    
        this.imgList.push("https://s3.wasabisys.com/homebyz/t_".concat(this.updationProduct.productImageId.pImage[i]));
      }


    this.prodUpdateForm = new FormGroup({
      productimgs: new FormControl(''),
      catalogueLbl: new FormControl(),
      prodTitle: new FormControl(),
      prodDesc: new FormControl(),
      prodWeight: new FormControl(),
      catlogTags: new FormControl(),
      price: new FormControl(),
      prodCount: new FormControl(),
      tags: new FormControl(),
      length: new FormControl(),
      breadth: new FormControl(),
      height: new FormControl(),
      availability: new FormControl(),

    });
    this.restrictBrowserBackButton()
  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  ngOnInit(): void {
    // this.spinnerService.show();
    this.updationProduct.productImageId.pImage.forEach((element: any) => {
      this.imgList.push(element);
    });
    this.firstImg = this.imgList[0];

    this.prodUpdateForm.controls.productimgs.setValue(this.imgList);

    this.imgList.splice(0, 1);

    this.imgList.splice(this.firstImg, 0);
    this.prodUpdateForm.controls.prodTitle.setValue(this.updationProduct.name);
    this.prodUpdateForm.controls.prodDesc.setValue(this.updationProduct.description);
    this.prodUpdateForm.controls.prodWeight.setValue(this.updationProduct.weight);
    this.prodUpdateForm.controls.price.setValue(this.updationProduct.basePrice);
    this.prodUpdateForm.controls.prodCount.setValue(this.updationProduct.quantity);
    this.prodUpdateForm.controls.tags.setValue(this.updationProduct.tags);
    this.prodUpdateForm.controls.length.setValue(this.updationProduct.length);
    this.prodUpdateForm.controls.breadth.setValue(this.updationProduct.breadth);
    this.prodUpdateForm.controls.height.setValue(this.updationProduct.height);
    this.prodUpdateForm.controls.availability.setValue(this.updationProduct.availability);

    this.spinnerService.hide();
  }
  stripText(event: any) {
    const seperator = '^([0-9])';
    const maskSeperator = new RegExp(seperator, 'g');
    let result = maskSeperator.test(event.key);

    return result;
  }
  onKeyUp(event: KeyboardEvent): void {
    const inputValue: string = this.prodUpdateForm.controls.catlogTags.value;
    if (event.code === 'Backspace' && !inputValue) {
      this.removeTag();
      return;
    } else {
      if (event.code === 'Comma' || event.code === 'Space') {
        this.addTag(inputValue);
        this.prodUpdateForm.controls.catlogTags.setValue('');
      }
    }
  }

  addTag(tag: string): void {
    this.tags.push(tag);
    console.log(this.tags);

  }

  removeTag(tag?: string): void {
    this.tags.forEach((element: any, index: any) => {
      if (element == tag) {
        this.tags.splice(index, 1);
      }

    });
  }

  addTagsToForm(tag: string) {

    this.tags.push(tag);
    console.log(this.tags);



    console.log(this.prodUpdateForm.value.catlogTags);
  }

  updateProduct() {
    this.spinnerService.show();
    this.prodUpdateForm.controls.catlogTags.setValue(this.tags);

    this.catalogues.forEach((element: any) => {
      if (element.subCatName == this.prodUpdateForm.controls.catalogueLbl.value) {
        this.prodUpdateForm.controls.catalogueLbl.setValue(element._id)
      }

    });
    // this.updationProduct.productImageId.pImage.forEach((element:any) => {
    //   this.imagefiles.push(element)

    // });
    // this.imgList.forEach((element:any) => {
    // 
    //   this.imagefiles.push(element);

    // });
    console.log(this.oldImage);
    console.log(this.removedImages);
    
    this.removedImages.forEach((element: any) => {
      var str = element.split("valart/", 2);
      this.oldImage.push(str[1]);


    }); 

    //  this.prodUpdateForm.controls.productimgs.setValue(this.imagefiles);


    this.ProductServiceObj.updateProduct(this.prodUpdateForm, this.updationProduct._id, this.imagefiles, this.oldImage).subscribe((response: any) => {
      console.log(response);
      this.spinnerService.hide();
      //  alert("product updated");
      this.router.navigate(['/admin/catalogues']);
    }, (error) => {
      this.spinnerService.hide();
      alert("product not updated.. samething wrong...")
    })
  }

  showPic(event: any) {

    const file = (event.target).files[0];
    this.imagefiles.push(file);
    const reader = new FileReader();

    reader.onload = () => {

      this.imgurl = reader.result as string;
      this.imgUrllist.push(this.imgurl);
    }
    reader.readAsDataURL(file);


  }

  removeImg(img: any, index: any) {

    if (img == this.firstImg) {
      this.firstImg = this.imgList[0];
      this.imgList.splice(0, 1);
      this.prodUpdateForm.controls.productimgs.setValue(this.imgList);
      this.removedImages.push(img);

    }
    else

      if (this.imgList.includes(img)) {
        this.imgList.splice(index, 1);
        this.prodUpdateForm.controls.productimgs.setValue(this.imgList);
        this.removedImages.push(img);
      }


      else if (this.imgUrllist.includes(img)) {

        this.imgUrllist.splice(index, 1);
        this.prodUpdateForm.controls.productimgs.setValue(this.imgUrllist);
        // this.oldImage.splice(index,1);
      }




  }
  // open mobile slider
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }

}
