import { LocationStrategy } from '@angular/common';
import { CompileShallowModuleMetadata } from '@angular/compiler';
import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { NgxSpinnerService } from 'ngx-spinner';
import { CatalogueService } from 'src/app/Services/catalogue.service';
import { DataService } from 'src/app/Services/data.service';
import { ProductService } from 'src/app/Services/product.service';
import { CatalogueHeaderComponent } from '../catalogue-header/catalogue-header.component';

class catalogue {
  public subCatName: String = "";
  public dispdiv: boolean = false;
  public product: any = [];

}

@Component({
  selector: 'app-catalogues',
  templateUrl: './catalogues.component.html',
  styleUrls: ['./catalogues.component.scss']
})
export class CataloguesComponent implements OnInit {

  cataloglist: any = [];
  Products: any = [];
  openslider: boolean = false;
  catalogues: any = [];
  constructor(private catheaderobj: CatalogueHeaderComponent,
    private location: LocationStrategy,
    private SpinnerService: NgxSpinnerService, private CatalogueServiceobj: CatalogueService,
    private productServiceObj: ProductService, private router: Router, private data: DataService) {

    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
    this.getAllCatalog();



    //console.log("this.cataloglist:"+ this.cataloglist);
  }

  ngOnInit(): void {

  }
  addProduct() {
    this.router.navigateByUrl('/admin/addproducts')
  }
  getAllCatalog() {
   
    this.SpinnerService.show();
    this.CatalogueServiceobj.getAllCatalogue().subscribe((response) => {

      this.catalogues = response;
      this.getAllProducts();
    });

  }

  toggle(itemname: any) {
   
    this.cataloglist.forEach((item: any) => {
      if (item.subCatName == itemname) {
        item.dispdiv = !item.dispdiv;

      }
    });
  }

  getAllProducts() {
    this.productServiceObj.fetchAllProduct().subscribe((response: any) => {
     
      this.Products = response;
      console.log("producs");
      console.log(this.Products);
      this.showLabelsWithProduct();
      this.SpinnerService.hide();
    }, (error) => {
      this.SpinnerService.hide();
    })
  }


  showLabelsWithProduct() {
    for (var i = 0; i < this.catalogues.length; i++) {
      var displaycatalog = new catalogue();
      displaycatalog.subCatName = this.catalogues[i].subCatName;
      displaycatalog.dispdiv = false;
      for (var j = 0; j < this.Products.length; j++) {
       
        if (this.catalogues[i]._id == this.Products[j].subCategoryId) {

          for (var k = 0; k < this.Products[j].productImageId.pImage.length; k++) {
            this.Products[j].productImageId.pImage[k] = "https://s3.wasabisys.com/homebyz/t_".concat(this.Products[j].productImageId.pImage[k]);
          }
          var prodArray: any = [];

          prodArray = this.Products[j];
         
          displaycatalog.product.push(prodArray);

        }

      }
     
      this.cataloglist.push(displaycatalog);
    }
    this.SpinnerService.hide();


  }

  updateProduct(product: any) {

    this.router.navigate(['/admin/updateproducts']);
   

    this.data.setUpdateProduct(product);
  }

  deleteProd(product: any) {
    console.log("delete" + product);

    this.productServiceObj.deleteProduct(product._id).subscribe((Response: any) => {

      console.log("res:" + Response);

      window.location.reload();

    })

  }
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }
}
