import { Component, Input, OnDestroy, OnInit } from '@angular/core';
import { CatalogueService } from 'src/app/Services/catalogue.service';
import { ProductService } from 'src/app/Services/product.service';

@Component({
  selector: 'app-catalogue-header',
  templateUrl: './catalogue-header.component.html',
  styleUrls: ['./catalogue-header.component.scss']
})
export class CatalogueHeaderComponent implements OnInit, OnDestroy {
  Products: any = []
  mngbtn: boolean = false;
  catalogues: any = [];
  forDashbord = false;
  @Input() fromDashordComp: any;
  constructor(private CatalogueServiceObj: CatalogueService, private ProductServiceObj: ProductService) {
   
    this.CatalogueServiceObj.getAllCatalogue().subscribe((response: any) => {
      this.catalogues = response;
    });
    this.ProductServiceObj.fetchAllProduct().subscribe((response: any) => {
      this.Products = response;
    })
  }
  ngOnDestroy(): void {
    this.forDashbord = false;
    this.fromDashordComp = null;
  }

  ngOnInit(): void {
    if (this.fromDashordComp) {
      console.log(this.fromDashordComp);
      this.forDashbord = true
    }
  }

  changeMngbtn() {
    this.mngbtn = true;

  }

}
