import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { VideoStreamService } from 'src/app/Services/video-stream.service';

@Component({
  selector: 'app-video-order',
  templateUrl: './video-order.component.html',
  styleUrls: ['./video-order.component.scss']
})
export class VideoOrderComponent implements OnInit {
  openslider: boolean = false;
  storeId:any
  orderedVideoData:any[] = [];
  imgUrl: any = 'https://valart.s3.us-east-1.wasabisys.com/';
  constructor(private videoService:VideoStreamService,   private SpinnerService: NgxSpinnerService) {
    var currentUser:any =  localStorage.getItem('currentUser');
    if(currentUser){
      let userData = JSON.parse(currentUser);
      this.storeId = userData.user.storeId;
    }
   }

  ngOnInit(): void {
    this.getOrderedVideosdata();
  }
  clickEvent() {
    this.openslider = !this.openslider;
  }

  getOrderedVideosdata = () => {
    this.videoService.getVideoOrdersByStoreId(this.storeId).subscribe((res:any) => {
        console.log("RES : ",res);
        res.results.forEach((element:any) => {
          if(element.liveVideoIds.length > 0 || element.packageVideoIds.length > 0 || element.recorededVideoIds.length > 0) {
            this.orderedVideoData.push(element);
          }      
        });
        console.log("Ordered Video Data : ", this.orderedVideoData);
      },
      (error:any)=>{
        console.log("ERR : ",error);
    });

  }

  cancelOrder(id:any){
    this.SpinnerService.show();
    this.videoService.cancelOrder(id).subscribe((res:any)=>{
      this.SpinnerService.hide();
      console.log("Res : ",res);
      this.getOrderedVideosdata();
    },(err:any)=>{
      console.log(err);
      this.SpinnerService.hide();
    })

  }
}
