import { LocationStrategy } from '@angular/common';
import { ThrowStmt } from '@angular/compiler';
import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { AdminService } from 'src/app/Services/admin.service';
import { Category1Component } from 'src/app/super-admin/category1/category1.component';
import { environment } from 'src/environments/environment';

@Component({
  selector: 'app-patron',
  templateUrl: './patron.component.html',
  styleUrls: ['./patron.component.scss']
})
export class PatronComponent implements OnInit {
  p: number = 1;
  openslider: boolean = false;
  petrons: any = [];
  term: any;
  petonLength: any;
  ActiveUser: any = [];

  constructor(private adminService: AdminService, private location: LocationStrategy, private SpinnerService: NgxSpinnerService) {
    this.restrictBrowserBackButton();
  }

  ngOnInit(): void {
    this.getPetron();

  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  setUser(ele: any) {
    this.ActiveUser = [];

    this.ActiveUser = ele;
    console.log(this.ActiveUser);




  }
  getPetron() {

    // const storeId = localStorage.getItem("storeId");
    this.SpinnerService.show();
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);

    const storeId = store.user.storeId;
    console.log(storeId);

    this.adminService.getPetron(storeId).subscribe((res: any) => {
      console.log(res);
      this.SpinnerService.hide();
      const data = res.data;
      this.petonLength = res.data.length;

      console.log(data);

      for (var i = 0; i < data.length; i++) {
        if (data[i].userId == null || data[i].userId == "undifined") {
          data.splice(i, 1)
        }
        if (data[i].userId.profileImageId == null || data[i].userId.profileImageId == "undifined") {
          data.splice(i, 1)
        }
      }
      console.log(data);

      const p1 = [];
      for (let i of data) {
        p1.push({
          city: i.userId.city,
          fullName: i.userId.fullName,
          mobile: i.userId.mobile,
          code: i.userId.code,
          id: i._id,
          email: i.userId.email,
          userId: i.userId,
          gender: i.userId.gender,
          pincode: i.userId.pincode,
          image: i.userId.profileImageId.image,
          state: i.userId.state,
          activationSatus: i.userId.activationSatus,
          role: i.userId.role,
          storeAdmin: false
        })
      }
      this.petrons = p1;
      this.petrons.forEach((ele: any) => {
        if (ele.image != null) {
          ele.image = "https://s3.wasabisys.com/valart/" + ele.image;
        }
        if (ele.image == null) {
          ele.image = "assets/images/user.jpg";
        }
        if (ele.role.includes("storeAdmin")) {
          ele.storeAdmin = true
        }
      });
      console.log(this.petrons);

    })

  }

  // open mobile slider
  clickEvent(event: any) {


    this.openslider = !this.openslider;
  }
}
