import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Component, OnInit } from '@angular/core';
import { ChartDataSets, ChartOptions, ChartType } from 'chart.js';
import { Label } from 'ng2-charts';
import { environment } from 'src/environments/environment';
import moment from 'moment';
import { map } from 'jquery';
@Component({
  selector: 'app-bar-garph',
  templateUrl: './bar-garph.component.html',
  styleUrls: ['./bar-garph.component.scss']
})
export class BarGarphComponent implements OnInit {
  elements: any = [];
  public barChartOptions: ChartOptions = {
    responsive: true,
  };
  // public barChartLabels: Label[] = ['2006', '2007', '2008', '2009', '2010', '2011', '2012'];
  public barChartLabels: Label[] = [];
  public barChartType: ChartType = 'bar';
  public barChartLegend = true;
  public barChartPlugins = [];

  public barChartData: ChartDataSets[] = [
    // { data: [], label: 'Series A' },
    // { data: [28, 48, 40, 19, 86, 27, 90], label: 'Series B' }
  ];
  Dates: any;
  graphDaa: any;
  graphData: any;

  constructor(private httpClient: HttpClient) { }

  ngOnInit() {
    this.getData();
  }
  getData() {
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    var storeId = store.user.storeId;
    var token = store.token;
    const headers = new HttpHeaders({ 'Content-Type': 'application/json', 'Authorization': token });
    const url = environment.BASE_URL + "/dashboard/orders/" + storeId;
    return this.httpClient.get(url, { headers: headers }).subscribe((res: any) => {
      //  console.log(res);
      //    console.log(res.data);
      this.Dates = res.data.map((x: any) => x._id);
      this.Dates = moment(this.Dates).format('Do MMMM YYYY')

      for (let i of res.data) {
        this.elements.push({
          label: moment(i._id).format('Do MMMM YYYY'),
          data: i.orders.length
        })

      }
      //  console.log(this.elements);
      const s4 = this.elements.map((x: any) => x.label);
      var mapobj = new Map()
      for (var i = 0; i < s4.length; i = i + 1) {
        if (mapobj.has(s4[i])) {
          var count = mapobj.get(s4[i])
          count = count + 1
          mapobj.set(s4[i], count)
        } else {
          mapobj.set(s4[i], 1)
        }
      }
      var s5 = [...mapobj]
      const s6 = [];
      for (let i of s5) {
        s6.push({
          "label": i[0],
          "data": i[1]
        })
        this.barChartLabels.push(i[0]);
      }
      const s7 = s6.map((x: any) => x.data);
      // console.log(s7);
      this.barChartData.push({
        "data": s7,
        "label": "Orders by date"
      })
      // console.log(this.barChartData);

      for (var i = 0; i < this.barChartData.length; i++) {
        if (this.barChartData[0]) {
          this.barChartData.splice(i, 1)
        }
      }
      //    console.log(this.barChartData);

    }, (error) => {
      console.log(error);

    })
  }
}


