import { Component, OnInit } from '@angular/core';
import { OrderService } from '../../../../Services/order.service';
import { Router } from '@angular/router';
import { SuperAdminService } from '../../../../Services/super-admin.service';
import {
  FormGroup,
  FormBuilder,
  FormControl,
  Validators,
} from '@angular/forms';

@Component({
  selector: 'app-payment-list',
  templateUrl: './payment-list.component.html',
  styleUrls: ['./payment-list.component.scss'],
})
export class PaymentListComponent implements OnInit {
  openslider: boolean = false;
  allOrders: any;
  settlementStatus: any;
  public filterForm!: FormGroup;
  recordNotFoundMsg!: string;
  filterFlag: boolean = false;
  p: number = 1;
  constructor(
    private OrderServiceObj: OrderService,
    private router: Router,
    private fb: FormBuilder,
    private apiService: SuperAdminService
  ) {
    this.settlementStatus = [
      { value: true, name: 'Released' },
      { value: false, name: 'Pending' },
    ];
  }

  ngOnInit(): void {
    this.fetchallOrders();
    this.formInit();
  }
  // convenience getter for easy access to form fields
  public get f() {
    return this.filterForm.controls;
  }

  formInit() {
    this.filterForm = this.fb.group({
      start_date: new FormControl('', Validators.compose([])),
      end_date: new FormControl('', Validators.compose([])),
      paymentReleased: new FormControl('', Validators.compose([])),
    });
  }

  clickEvent() {
    this.openslider = !this.openslider;
  }

  clearFilter() {
    this.filterForm.reset();
    this.filterFlag = false;
    this.recordNotFoundMsg = '';
    this.fetchallOrders();
  }

  applyFilter() {
    this.filterFlag = true;
    console.log('Data', this.filterForm.value);
    // if(this.filterForm.value.start_date  || this.filterForm.value.end_date )

    let startDate: any;
    let endDate: any;
    startDate = this.filterForm.value.start_date
      ? new Date(this.filterForm.value.start_date).toUTCString()
      : null;
    endDate = this.filterForm.value.end_date
      ? new Date(this.filterForm.value.end_date).toUTCString()
      : null;

    const requestObject = {
      start_date: startDate,
      end_date: endDate,
      paymentReleased: this.filterForm.value.paymentReleased,
    };
    console.log('reqObj', requestObject);

    this.apiService.applyPaymentFilter(requestObject).subscribe((res: any) => {
      console.log('res', res);
      if (!res.data) {
        this.recordNotFoundMsg = res.message;
      }
      this.allOrders = res?.data || [];
      this.processData();
    });
  }
  fetchallOrders() {
    this.OrderServiceObj.getOrderByStoreId().subscribe((response: any) => {
      this.allOrders = response.results;
      // console.log('this.allOrders', this.allOrders);
      this.processData();
    });
  }

  orderDeatils(OrderId: any) {
    this.router.navigate(['/admin/payment-details/' + OrderId]);
  }

  onOptionChange(event: any) {
    console.log(event.target.value);
    this.filterForm.value.paymentReleased = event.target.value;
  }

  processData() {
    console.log(this.allOrders);

    this.allOrders.forEach((element: any) => {
      if (element.food != 0) {
        element.product = element.food;
      }
      let res = 0;
      element.comissionCharges = 0;
      element.outgoingAmount = 0;
      let individualTaxAmount = 0;
      let cancellationAmountGst = 0;
      let totalSellingPrice = 0;

      let totalBasePrice = 0;
      element.product.forEach((item: any) => {
        if (item.shiprocket_delivery_charges) {
          res += item.shiprocket_delivery_charges;
        } else {
          item.shiprocket_delivery_charges = 0;
        }
        let result = 0;
        let outgoingAmt = 0;

        if (item.hasOwnProperty('productId')) {
          if (element.product) {
            totalBasePrice += parseInt(item.productId.basePrice);
          }

          totalSellingPrice += item.productSellingPrice;

          if (element.storeId.gst === true) {
            individualTaxAmount +=
              (parseInt(item.productId.basePrice) / 100) * element.taxRate;
            cancellationAmountGst +=
              (item.productSellingPrice / 100) * element.taxRate;
          } else {
            individualTaxAmount = 0;
          }

          if (item?.productId) {
            result =
              item.productSellingPrice - parseInt(item?.productId?.basePrice);
          } else {
            result = item.productSellingPrice - item?.productPrice;
          }
          outgoingAmt = item?.productSellingPrice;
        } else {
          if (element.product) {
            totalBasePrice += parseInt(item.foodPrice);
          }
          if (element.storeId.gst === true) {
            individualTaxAmount +=
              (item.foodSellingPrice / 100) * this.allOrders.taxRate;
          } else {
            individualTaxAmount = 0;
          }

          result = item?.foodSellingPrice - item?.foodPrice;
          outgoingAmt = item?.foodSellingPrice;
        }
        element.comissionCharges += result;
        element.outgoingAmount += outgoingAmt;
        if (item.isCanceled == true) {
          element.cancellationCharges = (
            cancellationAmountGst +
            res +
            totalSellingPrice
          ).toFixed(2);
        } else {
          element.cancellationCharges = 0;
        }
      });
      element.cancellationAmountGst = cancellationAmountGst;
      element.totalGst = individualTaxAmount.toFixed(2);
      element.totalBasePrice = totalBasePrice;
      element.totalSellingPrice = totalSellingPrice;
      element.comissionCharges = element.comissionCharges.toFixed(2);

      element.requestedAmount =
        parseInt(element.totalBasePrice) + parseInt(element.totalGst);
      element.shiprocket_delivery_charges = res;
    });

    console.log(this.allOrders);
  }
}
