import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { EditAddressComponent } from '../edit-address/edit-address.component';
import { VideosGardService } from '../Services/videos-gard.service';
import { AddProductsComponent } from './components/Catalogues/add-products/add-products.component';
import { CataloguesComponent } from './components/Catalogues/catalogues/catalogues.component';
import { ManageCatalogueComponent } from './components/Catalogues/manage-catalogue/manage-catalogue.component';
import { UpdateProductComponent } from './components/Catalogues/update-product/update-product.component';
import { ChatComponent } from './components/chat/chat.component';
import { CutomerListComponent } from './components/customer/cutomer-list/cutomer-list.component';
import { DashboardComponent } from './components/dashboard/dashboard.component';
import { EditAadharComponent } from './components/edit-aadhar/edit-aadhar.component';
import { EditBankDetailsComponent } from './components/edit-bank-details/edit-bank-details.component';
import { AddFoodMenuComponent } from './components/menu/add-food-menu/add-food-menu.component';
import { FoodMenuComponent } from './components/menu/food-menu/food-menu.component';
import { ManageFoodMenuComponent } from './components/menu/manage-food-menu/manage-food-menu.component';
import { UpdateFoodComponent } from './components/menu/update-food/update-food.component';
import { Message1Component } from './components/message/message1/message1.component';
import { OrderDetailsComponent } from './components/order/order-details/order-details.component';
import { OrderDetails1Component } from './components/order/order-details1/order-details1.component';
import { OrderListComponent } from './components/order/order-list/order-list.component';
import { VideoOrderComponent } from './components/order/video-order/video-order.component';
import { PatronComponent } from './components/patron/patron/patron.component';
import { PersonalInfoComponent } from './components/personal-info/personal-info.component';
import { ReviewListComponent } from './components/review-list/review-list.component';
import { ReviewsComponent } from './components/reviews/reviews.component';
import { ValstoreFrontComponent } from './components/valstore-front/valstore-front.component';
import { AddVideoStreamComponent } from './components/video-stream/add-video-stream/add-video-stream.component';
import { LiveStreamDetailComponent } from './components/video-stream/live-stream-detail/live-stream-detail.component';
import { ManageVideoStreamComponent } from './components/video-stream/manage-video-stream/manage-video-stream.component';
import { VideoStreamComponent } from './components/video-stream/video-stream/video-stream.component';
import { PaymentListComponent } from './components/payments/payment-list/payment-list.component';
import { PaymentDetailsComponent } from './components/payments/payment-details/payment-details.component';
import { AdminAuthGardService } from '../Services/admin-auth-gard.service';
import { VideoOrderDetailComponent } from './components/order/video-order-detail/video-order-detail.component';

const routes: Routes = [
  {
    path: '',
    redirectTo: 'dashboard',
    pathMatch: 'full',
  },
  {
    path: 'dashboard',
    component: DashboardComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'catalogues',
    component: CataloguesComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'mngcatlbls',
    component: ManageCatalogueComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'addproducts',
    component: AddProductsComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'updateproducts',
    component: UpdateProductComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'order-list',
    component: OrderListComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'order-details',
    component: OrderDetailsComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'order-detail/:id',
    component: OrderDetails1Component,
    canActivate: [AdminAuthGardService],
  },
  { path: 'account', component: ValstoreFrontComponent },
  {
    path: 'patron',
    component: PatronComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'video-orders',
    component: VideoOrderComponent,
    canActivate: [VideosGardService],
  },
  {
    path: 'video-orders-detail',
    component: VideoOrderDetailComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'customer-list',
    component: CutomerListComponent,
    canActivate: [AdminAuthGardService],
  },
  // { path: 'messages', component: ChatComponent },
  {
    path: 'messages',
    component: Message1Component,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'review-list',
    component: ReviewListComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'reviews',
    component: ReviewListComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'review-detail',
    component: ReviewsComponent,
    canActivate: [AdminAuthGardService],
  },
  { path: 'chat', component: ChatComponent },
  {
    path: 'video-stream',
    component: VideoStreamComponent,
    canActivate: [VideosGardService],
  },
  { path: 'add-video-stream', component: AddVideoStreamComponent },
  { path: 'update-video-stream/:id', component: AddVideoStreamComponent },
  { path: 'manage-video-stream', component: ManageVideoStreamComponent },
  {
    path: 'menu',
    component: FoodMenuComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'add-food',
    component: AddFoodMenuComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'updatefood',
    component: UpdateFoodComponent,
    canActivate: [AdminAuthGardService],
  },
  {
    path: 'manage-food-menu',
    component: ManageFoodMenuComponent,
    canActivate: [AdminAuthGardService],
  },
  { path: 'addressdetail', component: EditAddressComponent },
  { path: 'bankdetails', component: EditBankDetailsComponent },
  { path: 'editAdhar', component: EditAadharComponent },
  { path: 'personalinfo', component: PersonalInfoComponent },
  { path: 'live-stream-detail/:id', component: LiveStreamDetailComponent },
  { path: 'payment-list', component: PaymentListComponent },
  { path: 'payment-details/:id', component: PaymentDetailsComponent },
  {
    path: '**',
    redirectTo: 'dashboard',
    pathMatch: 'full',
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class AdminRoutingModule {}
