import { Component, OnInit, ViewChild } from '@angular/core';
import { FoodMenuService } from 'src/app/Services/food-menu.service';
import { FoodHeaderComponent } from '../food-header/food-header.component';
import { NgxSpinnerService } from "ngx-spinner";
import { CatalogueService } from 'src/app/Services/catalogue.service';
import { MatDialog } from '@angular/material/dialog';
import { LocationStrategy } from '@angular/common';
@Component({
  selector: 'app-manage-food-menu',
  templateUrl: './manage-food-menu.component.html',
  styleUrls: ['./manage-food-menu.component.scss']
})
export class ManageFoodMenuComponent implements OnInit {
  @ViewChild("catalogue", { static: false }) catalogue: any;
  label: any = "";
  MenuLabels: any = [];
  catToBeDeteletd: any;
  catToBeEditedId: any;
  openslider: boolean = false;
  catalogToBeEdited: any;
  alertMsg: any;
  constructor(private dialog: MatDialog, private catlogService: CatalogueService, private location: LocationStrategy, private foodMenuService: FoodMenuService, private SpinnerService: NgxSpinnerService, private CatalogueServiceObj: CatalogueService) {
    this.restrictBrowserBackButton();
  }
  restrictBrowserBackButton() {
    //private location: LocationStrategy,
    // for restrict back button 
    var currentUser: any = localStorage.getItem('currentUser');
    const store = JSON.parse(currentUser);
    const role = store.user.role;
    if (role.includes("superAdmin")) {
      history.pushState(null, window.location.href);
      this.location.onPopState(() => {
        history.pushState(null, window.location.href);
      });
    }
  }
  ngOnInit(): void {
    this.getMenulabel();
  }
  handleError() {
    this.alertMsg = null;
  }

  getMenulabel() {
    this.foodMenuService.getMenuLabels().subscribe((response: any) => {
      console.log(" MenuLabels :", response);
      // ;
      this.MenuLabels = response;
      this.SpinnerService.hide();
    }, (error) => {
      this.SpinnerService.hide();
    });
  }

  addNewLabel() {

    if (this.label != "") {
      // let newMenuItem = {
      //   id: this.MenuLabels.length+1,
      //   display: false,
      //   name: this.label
      // }
      this.SpinnerService.show();
      this.foodMenuService.createMenuLabel(this.label).subscribe((response: any) => {
        console.log(response);
        this.label = "";
        this.getMenulabel();
        this.label = "";
        this.SpinnerService.hide();
      });
      // this.MenuLabels.push(newMenuItem);

    } else {
      this.alertMsg = "Enter Label name First";
    }
  }

  removecatlog(catName: any) {
    this.MenuLabels.forEach((element: any) => {
      if (element.subCatName == catName) {
        // ;
        this.catToBeDeteletd = element._id;
        // this.dialog.closeAll();
        // this.dialog.open(this.catalogue, {
        //   panelClass: ['modal_center', 'material_modal_center']
        // });
      }

    });
    console.log(catName);
    console.log(this.catToBeDeteletd);

    this.SpinnerService.show();

    this.catlogService.verifySubcategory(this.catToBeDeteletd).subscribe((res: any) => {
      console.log(res);
      this.SpinnerService.hide();

      if (res.message == "This category contains food item. ") {
        this.dialog.open(this.catalogue, {
          panelClass: ['modal_center', 'material_modal_center']
        });

      }

      this.MenuLabels = [];
      this.getMenulabel();
    }, (error) => {
      console.log(error);

      this.SpinnerService.hide();
    })

  }
  deleteCatlogLabelAndProd() {
    this.SpinnerService.show();
    //deleteCatalogLabels
    this.CatalogueServiceObj.deleteCatalogLabels(this.catToBeDeteletd).subscribe((response: any) => {

      console.log(response);
      this.dialog.closeAll();
      this.SpinnerService.hide();

      window.location.reload();


    });

    //   this.router.navigate(['/admin/mngcatlbls']);
  }
  // open mobile slider
  clickEvent(event: any) {

    this.openslider = !this.openslider;
  }

  editCatalog(catlog: any) {
    this.catalogToBeEdited = catlog;
    this.MenuLabels.forEach((element: any) => {
      if (element.subCatName == catlog) {

        this.catToBeEditedId = element._id;
      }

    });

  }

  submitCatalog() {
    this.CatalogueServiceObj.editCatalogue(this.catalogToBeEdited, this.catToBeEditedId).subscribe(
      (response: any) => {
        // ;
        this.alertMsg = "menu name updated successfully";
      },
      (error: any) => {
        // ;
        this.alertMsg = error;
      }

    )

  }
}
