async saveCoverPic(file:any){
  this.SpinnerService.show();
  var response:any=await this.ValstoreRegisterServiceObj.uploadCoverPic(file).toPromise();
  if(response.success)
{
  alert(response.message);
  this.SpinnerService.hide();
  window.location.reload();
}
else{
  alert(response.message);
  this.SpinnerService.hide();
  window.location.reload();
}
}


async saveLogo(file:any){
  this.SpinnerService.show();
  var response:any=await this.ValstoreRegisterServiceObj.uploadLogo(file).toPromise();
  if(response.success)
{
  alert(response.message);
  this.SpinnerService.hide();
  window.location.reload();
}
else{
  alert(response.message);
  this.SpinnerService.hide();
  window.location.reload();
}
}

Delete(value:any){
 
  if(value=="coverpic"){
    var file=null;
    this.saveCoverPic(file);
  }
  else{
   
    var file=null;
    this.saveLogo(file);
  }
}



